<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <!-- 替换为搜索内容 -->
            </div>
        </div>

        <div class="row layer-box room-createHotel">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">新增酒店</span>
                    <span class="rightTotal"></span>
                </div>
                <div class="user-msgs-table layerC-content-createHotel">
                    <template>
                        <el-table
                            :data="hotelList"
                            style="width: 100%"
                            class="element-table-test"
                            height="260"
                        >
                            <el-table-column type="index" label="序号" width="70"></el-table-column>
                            <el-table-column prop="hotelName" label="酒店名称" width="150"></el-table-column>
                            <el-table-column prop="hotelDesc" label="酒店描述" width="130"></el-table-column>
                            <el-table-column prop="hotelCreator" label="创建者" width="150"></el-table-column>
                            <el-table-column prop="hotelAttr" label="酒店地址" width="150"></el-table-column>
                            <el-table-column
                                prop="createTime"
                                label="创建时间"
                                width="130"
                                :formatter="formatDate"
                            ></el-table-column>
                            <el-table-column prop="type" fixed="right" label="操作" width="150">
                                <template slot-scope="scope">
                                    <div
                                        @click="handleUpdate(scope.$index, scope.row)"
                                        class="updata tableBtn icon-2x8"
                                    ></div>
                                    <div
                                        @click="handleDelete(scope.$index, scope.row)"
                                        class="delete tableBtn icon-2x12"
                                    ></div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
                <ul class="user-table-bottom row">
                    <li class="add-user col-xs-2 col-sm-2 col-md-2 col-lg-2">
                        <button class="add-user-btn" @click="dialogShow">添加酒店</button>
                        <el-dialog
                            class="create-user-form"
                            :title="dialogData.title"
                            :visible.sync="dialogFormVisible"
                            center
                            :before-close="handleClose"
                        >
                            <ul class="form-list">
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">酒店名称</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            v-model="newHotel.hotelName"
                                            placeholder="请输入酒店名称"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">酒店地址</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            placeholder="请输入酒店地址"
                                            v-model="newHotel.hotelAttr"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">所属组织</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            placeholder="请选择所属组织"
                                            v-model="newHotel.deptName"
                                            v-on="{focus:codefocus,blur:codeblur}"
                                        ></el-input>
                                        <el-dialog
                                            class="choose-organ-dialog"
                                            title="请选择"
                                            :visible.sync="dialogFormVisiblezz"
                                            center
                                            append-to-body
                                        >
                                            <div>
                                                <el-tree
                                                    :data="organizaTree"
                                                    :props="defaultProps"
                                                    highlight-current
                                                    class="organ-el-tree"
                                                    @node-click="getNodeData"
                                                    :render-content="renderContent"
                                                ></el-tree>
                                            </div>
                                            <div slot="footer" class="dialog-footer">
                                                <el-button @click="dialogFormVisiblezz = false">取 消</el-button>
                                                <el-button
                                                    type="primary"
                                                    @click="organDialogConfirm"
                                                >确 定2</el-button>
                                            </div>
                                        </el-dialog>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">酒店描述</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            type="textarea"
                                            class="msg-input thenumber"
                                            placeholder="请输入酒店描述"
                                            :autosize="{ minRows: 2, maxRows: 4}"
                                            v-model="newHotel.hotelDesc"
                                        ></el-input>
                                    </div>
                                </li>
                                <!-- <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">角色</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-select
                                            v-model="newHotel.role"
                                            multiple
                                            placeholder="请选择"
                                            class="msg-input"
                                        >
                                            <el-option
                                                v-for="item in roleList"
                                                :key="item.roleId"
                                                :label="item.roleName"
                                                :value="item.roleId"
                                            ></el-option>
                                        </el-select>
                                    </div>
                                </li>-->
                            </ul>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="handleCancel">取 消</el-button>
                                <el-button type="primary" @click="handleConfirm">确 定1</el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="talbe-paging col-xs-10 col-sm-10 col-md-10 col-lg-10">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage4"
                            :page-sizes="[100, 200, 300, 400]"
                            :page-size="100"
                            layout=" prev, pager, next, jumper, sizes"
                            :total="400"
                        ></el-pagination>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import moment from "moment";
export default {
    name: "Createhotel",
    data: function() {
        return {
            lists: [],
            currentPage4: 1,
            dialogData: {
                title: "",
                type: 0 //1 新增 2 编辑
            },
            dialogFormVisible: false,
            dialogFormVisiblezz: false,
            newHotel: {
                hotelName: "",
                hotelAttr: "",
                deptId: "",
                deptName: "",
                hotelDesc: "",
                hotelId:""
            },
            defaultProps: {
                children: "children",
                label: "name"
            }
        };
    },
    computed: {
        ...mapState("room", ["hotelList"]),
        ...mapState("dept", [
            "userList",
            "roleList",
            "menuTree",
            "organizaTree"
        ])
    },
    methods: {
        ...mapActions("room", [
            "loadSysHotelList",
            "submitCreateHotel",
            "submitDeleteHotel",
            "putHotelDetail",
            "submitHotelDetail"
        ]),
        ...mapActions("dept", [
            "loadUserlist",
            "loadOrganizaTree",
            "loadMenuTree",
            "loadRoleList",
        ]),
        formatDate: function(date) {
            return moment(date).format("YYYY-MM-DD");
        },
        // 分页按钮
        handleSizeChange: function(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange: function(val) {
            console.log(`当前页: ${val}`);
        },
        handchange: function() {
            console.log(this.newHotel.phone);
        },
        dialogShow: function() {
            this.dialogData.title = "新建酒店";
            this.dialogData.type = 1;
            this.dialogFormVisible = true;
        },
        handleClose: function() {
            this.dialogFormVisible = false;
        },
        codefocus: function() {
            this.dialogFormVisiblezz = !this.dialogFormVisiblezz;
        },
        codeblur: function() {},
        getNodeData: function(datas) {
            console.log(datas);
            this.newHotel.deptName = datas.name;
            this.newHotel.deptId = datas.id;
        },
        renderContent: function(h, { node, data, store }) {
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        // 组织的弹窗 确认按钮
        organDialogConfirm: function() {
            this.dialogFormVisiblezz = false;
        },
        handleCancel: function() {
            this.dialogFormVisible = false;
            this.dialogFormdataEmpty();
        },
        // 创建酒店 确认
        handleConfirm: function() {
            console.log(this.dialogData.type);
            let obj = {};
            if (this.dialogData.type == 1) {
                obj.hotelName = this.newHotel.hotelName;
                obj.hotelAttr = this.newHotel.hotelAttr;
                obj.hotelDesc = this.newHotel.hotelDesc;
                obj.deptId = this.newHotel.deptId;
                console.log(obj);
                this.submitCreateHotel(obj);
            } else if (this.dialogData.type == 2) {
                obj.hotelName = this.newHotel.hotelName;
                obj.hotelAttr = this.newHotel.hotelAttr;
                obj.hotelDesc = this.newHotel.hotelDesc;
                obj.deptId = this.newHotel.deptId;
                  obj.hotelId=this.newHotel.hotelId;
                console.log(obj);
                this.submitHotelDetail(obj);
            }
        },
        // 删除酒店
        handleDelete: function(index, row) {
            console.log(row);
            // this.submitDeleteHotel(row.hotelId)
        },
        // 修改酒店
        handleUpdate: function(index, row) {
            console.log(row);
            this.dialogData.title = "编辑酒店";
            this.dialogData.type = 2;
            this.newHotel.hotelName = row.hotelName;
            this.newHotel.hotelAttr = row.hotelAttr;
            this.newHotel.deptId = row.deptId;
            this.newHotel.hotelId = row.hotelId;
            this.newHotel.deptName = row.deptName;
            this.newHotel.hotelDesc = row.hotelDesc;
            this.dialogFormVisible = true;
        },
        dialogFormdataEmpty: function() {
            this.newHotel.hotelName = "";
            this.newHotel.hotelAttr = "";
            this.newHotel.deptId = "";
            this.newHotel.deptName = "";
            this.newHotel.hotelDesc = "";
        }
    },
    mounted() {
        this.loadSysHotelList();
        this.loadUserlist();
        this.loadOrganizaTree();
        this.loadMenuTree();
        this.loadRoleList();
    }
};
</script>
<style lang="less">
.layerC-content-createHotel {
    min-height: 134px;
}
.room-createHotel {
    background: #fff;
}
.user-msgs-table {
    // max-height: 300px;
    padding: 0 12px 18px;
    height: 300px;
    overflow: auto;
    .el-table {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        // 表格头
        .el-table__header {
            th.is-leaf {
                border: 0 none;
            }
            thead {
                color: #303030;
                letter-spacing: -0.25px;
                font-weight: 500;
            }
            th {
                font-weight: 500;
            }
        }
        // 表格body
        .el-table__body {
            tbody tr {
                // border-bottom:1px solid #f00;
                &:nth-child(odd) {
                    background: #fefefe;
                }
                &:nth-child(even) {
                    background: #fafafa;
                }
            }
        }
    }
    .el-table:before {
        height: 0;
    }

    .el-table td,
    .el-table th.is-leaf {
        // border: 0 none;
    }
    // hover 时 正行的 颜色改变
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #eee !important;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
}
.user-table-bottom {
    height: 60px;
    padding: 12px 12px 24px 12px;
    margin-top: 20px;
    .add-user,
    .talbe-paging {
        height: 24px;
    }
    .add-user {
        .add-user-btn {
            height: 24px;
            font-family: PingFangSC-Semibold;
            font-size: 12px;
            color: #ffffff;
            background: #86b3e7;
            letter-spacing: -0.23px;
            text-align: center;
            padding: 0 12px;
            line-height: 24px;
            border-radius: 6px;
        }
    }
}
.user-msgs-table {
    // max-height: 300px;
    padding: 0 12px 18px;
    height: 300px;
    overflow: auto;
    .el-table {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        // 表格头
        .el-table__header {
            th.is-leaf {
                border: 0 none;
            }
            thead {
                color: #303030;
                letter-spacing: -0.25px;
                font-weight: 500;
            }
            th {
                font-weight: 500;
            }
        }
        // 表格body
        .el-table__body {
            tbody tr {
                // border-bottom:1px solid #f00;
                &:nth-child(odd) {
                    background: #fefefe;
                }
                &:nth-child(even) {
                    background: #fafafa;
                }
            }
        }
    }
    .el-table:before {
        height: 0;
    }

    .el-table td,
    .el-table th.is-leaf {
        // border: 0 none;
    }
    // hover 时 正行的 颜色改变
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #eee !important;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
}
.user-table-bottom {
    height: 60px;
    padding: 12px 12px 24px 12px;
    margin-top: 20px;
    .add-user,
    .talbe-paging {
        height: 24px;
    }
    .add-user {
        .add-user-btn {
            height: 24px;
            font-family: PingFangSC-Semibold;
            font-size: 12px;
            color: #ffffff;
            background: #86b3e7;
            letter-spacing: -0.23px;
            text-align: center;
            padding: 0 12px;
            line-height: 24px;
            border-radius: 6px;
        }
    }
}
.create-user-form {
    .el-dialog {
        // dialog 标题
        .el-dialog__header {
            .el-dialog__title {
                font-family: PingFangSC-Semibold;
                font-size: 14px;
                color: #303030;
                letter-spacing: -0.25px;
                text-align: center;
                font-weight: bolder;
            }
        }
    }
    .form-list {
        .form-list-li {
            margin-bottom: 20px;
            height: 28px;
            .name,
            .msg {
                float: left;
            }
            .name {
                // width: 92px;
                height: 28px;
                text-align: right;
                line-height: 28px;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #303030;
                letter-spacing: -0.25px;
            }
            .msg {
                padding-left: 16px;
                height: 28px;
                // width: 80%;
                .el-autocomplete {
                    width: 100%;
                }
                .el-select {
                    width: 100%;
                }
                .msg-input {
                    //  width: 100%;
                    .el-input__inner {
                        height: 28px;
                        border: 0 none;
                        background: #f4f4f4;
                        border-radius: 6px;
                    }

                    .el-input__suffix {
                        height: 28px;

                        .el-input__icon {
                            line-height: 28px;
                        }
                    }
                }
            }
        }
    }
}
</style>


