import Vue from 'vue';
import Vuex from 'vuex';
import testmod from './modules/testmod';
import roomController from './modules/roomController';
import management from './modules/management';
import sceneDevice from './modules/sceneDevice';
Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    test: testmod,
    room: roomController,
    dept: management,
    scene: sceneDevice,
  }
})



