<template>
    <div class="mainContR">
        <div class="row layer-box room-themenu">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">菜单管理</span>
                    <span class="rightTotal">共X个角色</span>
                </div>
                <div class="layerC-content-themenu row">
                    <div class="themenu-tree col-xs-4 col-sm-4 col-md-4 col-lg-4">
                        <el-tree
                            :data="menuTree"
                            :props="defaultProps"
                            class="themenu-el-tree"
                            highlight-current
                            @node-click="getNodeData"
                            :render-content="renderContent"
                        ></el-tree>
                        <ul class="themenu-btn-menu">
                            <li class="themenu-btn add" @click="handleAdd">添加菜单</li>
                            <li class="themenu-btn edit" @click="handleEdit">编辑菜单</li>
                            <li class="themenu-btn delete" @click="handleDelete">删除</li>
                        </ul>
                    </div>
                    <div class="themenu-treehandle col-xs-8 col-sm-8 col-md-8 col-lg-8">
                        <ul class="themenu-treehandle-list">
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">父级节点</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.parentId"
                                        :disabled="formEdit"
                                        placeholder="请输入父级节点"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">节点ID</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.deptId"
                                        placeholder="节点编号"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">标题</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.name"
                                        :disabled="formEdit"
                                        placeholder="请输入标题"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">权限标识</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.permission"
                                        placeholder="请输入权限标识"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">图标</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.icon"
                                        placeholder="请输入图标"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">类型</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.type"
                                        placeholder="请输入资源请求类型"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">权限排序</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.sort"
                                        placeholder="请输入排序"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">前端组件</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.component"
                                        placeholder="请输入描述"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">前段地址</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.path"
                                        placeholder="iframe嵌套地址"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">路由缓冲</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <template>
                                        <el-radio v-model="radio" label="1">打开</el-radio>
                                        <el-radio v-model="radio" label="2">关闭</el-radio>
                                    </template>
                                </div>
                            </li>
                            <li class="list-li row">
                                <div class="col-xs-11 col-sm-11 col-md-11 col-lg-11 btn-menu">
                                    <span class="save inputBtn" v-if="formStatus === 'create'" @click="handleSave">保存</span>
                                    <span class="update inputBtn" v-if="formStatus === 'update'" @click="handleUpdate">更新</span>
                                    <span class="cancel inputBtn" v-if="formStatus != ''"  @click="handleCancel">取消</span>
                                </div>
                                <div class="col-xs-1 col-sm-1 col-md-1 col-lg-1"></div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
    name: "Themenu",
    data: function() {
        return {
            defaultProps: {
                children: "children",
                label: "name"
            },
            treeForm: {
                parentId: "",
                deptId: "",
                name: "",
                sort: "",
                permission: "",
                icon: "",
                type: "",
                component: "",
                path: ""
            },
            radio: "1",
            formEdit: true,
            formStatus: "" //update 编辑  create 添加
        };
    },
    computed: {
        ...mapState("dept", ["menuTree"])
    },
    methods: {
        ...mapActions("dept", ["loadMenuTree"]),
        //树状图
        renderContent(h, { node, data, store }) {
            // console.log(node, data, store);
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        getNodeData: function(datas) {
            // console.log(datas);
            this.treeForm.parentId = datas.parentId;
            this.treeForm.deptId = datas.id;
            this.treeForm.name = datas.name;
            this.treeForm.permission = datas.permission;
            this.treeForm.icon = datas.icon;
            this.treeForm.type = datas.type;
            this.treeForm.component = datas.component;
            this.treeForm.path = datas.path;
            // this.formEdit = false;
        },
        //页面中的操作按钮
        handleAdd: function() {
            this.formEdit = false;
            this.formStatus = "create";
        },
        handleEdit: function() {
            this.formEdit = false;
            this.formStatus = "update";
        },
        handleDelete: function() {
            // this.formEdit = false;
        },
        // 列表中的功能按钮
        // 保存
        handleSave:function() {

        },
        // 更新
        handleUpdate:function() {

        },
        // 取消
        handleCancel:function() {

        },
    },
    mounted() {
        this.loadMenuTree();
    },
    updated() {}
};
</script>
<style lang="less">
.room-themenu {
    background: #fff;
}
.layerC-content-themenu {
    // // min-height: 134px;
    // .themenu-tree,
    // .themenu-treehandle {
    // }
    .themenu-tree {
        padding: 24px 0 0 24px;
        .themenu-el-tree {
            min-height: 244px;
        }
        .el-icon-caret-right {
            &:before {
                content: "";
            }
        }
        .el-tree-node__content {
            height: 32px;
            .custom-tree-node {
                height: 32px;
                line-height: 32px;
                .chlidIcon {
                    text-align: center;
                }
                .icon--8,
                .icon-close-2 {
                    &:before {
                        color: #999;
                    }
                }
            }
        }
    }
    .themenu-treehandle {
        .themenu-treehandle-list {
            padding: 24px 50px 0 24px;
            .list-li {
                margin-bottom: 20px;
                height: 28px;
                .name,
                .msg {
                    float: left;
                }
                .name {
                    // width: 92px;
                    height: 28px;
                    text-align: right;
                    line-height: 28px;
                    font-family: PingFangSC-Regular;
                    font-size: 12px;
                    color: #303030;
                    letter-spacing: -0.25px;
                }
                .msg {
                    padding-left: 16px;
                    height: 28px;
                    // width: 80%;
                    .msg-input {
                        .el-input__inner {
                            height: 28px;
                            border: 0 none;
                            background: #f4f4f4;
                            border-radius: 6px;
                        }
                    }
                }
                .btn-menu {
                    .inputBtn {
                        float: right;
                        width: 108px;
                        height: 30px;
                        line-height: 30px;
                        font-family: PingFangSC-Semibold;
                        font-size: 12px;
                        color: #ffffff;
                        letter-spacing: -0.25px;
                        text-align: center;
                        border-radius: 6px;
                        margin: 0 8px 0 0;
                        cursor: pointer;
                    }
                    .cancel {
                        background: #ffa265;
                    }
                    .save,
                    .update {
                        background: #86b3e7;
                    }
                }
            }
        }
    }
}

// 操作按钮
.themenu-btn-menu {
    height: 72px;
    padding: 24px;
    .themenu-btn {
        float: left;
        width: 64px;
        height: 24px;
        margin: 0 8px 0 0;
        border-radius: 6px;
        line-height: 24px;
        text-align: center;
        font-family: PingFangSC-Semibold;
        font-size: 12px;
        color: #ffffff;
        letter-spacing: -0.23px;
        cursor: pointer;
    }
    .themenu-btn.add {
        background: #c1e080;
    }
    .themenu-btn.edit {
        background: #86b3e7;
    }
    .themenu-btn.delete {
        background: #ffa265;
    }
}
</style>


