<template>
    <div class="slider-cont">
        <div class="divs">
            <ul class="leftSlide">
                <li class="slideLis-LOGO"></li>
                <li class="slideLis-homePage">首页</li>
                <li class="slideLis-msg">客房监控</li>
            </ul>
            <el-collapse
                v-model="activeName"
                accordion
                class="leftSlide"
                @change="handleChange"
            >
                <el-collapse-item
                    class="slideLis-msg"
                    title="运营管理"
                    name="2"
                    v-bind:class="{color60: !isshow,color30: isshow}"
                    @click.native="isshow = !isshow;"
                >
                    <div class="sliderChildrens">场景管理</div>
                    <div class="sliderChildrens">客房授权</div>
                    <div class="sliderChildrens">异常警告</div>
                    <div class="sliderChildrens">服务请求</div>
                    <div class="sliderChildrens">投诉建议</div>

                </el-collapse-item>
                <el-collapse-item
                    title="酒店信息"
                    name="3"
                >
                    <div class="sliderChildrens">楼宇信息</div>
                    <div class="sliderChildrens">楼房信息</div>
                    <div class="sliderChildrens">房型信息</div>
                    <div class="sliderChildrens">客房信息</div>
                    <div class="sliderChildrens">区域信息</div>
                    <div class="sliderChildrens">设备信息</div>
                    <div class="sliderChildrens">设备类型</div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title="统计查询"
                    name="4"
                >
                    <div class="sliderChildrens">能耗统计</div>
                    <div class="sliderChildrens">客人统计</div>
                    <div class="sliderChildrens">入住统计</div>
                    <div class="sliderChildrens">使用统计</div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title="账户管理"
                    name="5"
                >
                    <div class="sliderChildrens">权限管理</div>
                    <div class="sliderChildrens">用户管理</div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title="系统管理"
                    name="6"
                >
                    <div class="sliderChildrens">主机管理</div>
                    <div class="sliderChildrens">参数设置</div>
                    <div class="sliderChildrens">异常管理</div>
                    <div class="sliderChildrens">服务管理</div>
                    <div class="sliderChildrens">日志管理</div>
                </el-collapse-item>
                <el-collapse-item
                    class="slideLis-msg"
                    title="数据管理"
                    name="7"
                >
                    <div class="sliderChildrens">数据库备份</div>
                    <div class="sliderChildrens">数据库还原</div>
                    <div class="sliderChildrens">数据库定时器</div>
                </el-collapse-item>
            </el-collapse>
        </div>

    </div>
</template>

<script>
export default {
    methods: {
        handleNodeClick(data) {
            console.log(data);
        },
        handleChange(val) {
            console.log(val);
        },
        collapseClick(e) {
            console.log(e.target);
        }
    },
    data() {
        return {
            activeName: "",
            isshow: true
        };
    }
   
};
</script>
<style lang="less" scoped>
@import "../../../static/css/init.css";
.slider-cont {
    /* display: flex; */
    height: 100%;
}
.divs {
    width: 19.5%;
    float: left;
    background: #fff;
    height: 100%;
}
.leftSlide {
    // height: 100%;
    .slideLis-LOGO {
        height: 104px;
    }
    .slideLis-homePage {
        min-height: 48px;
        font-size: 13px;
        color: #303030;
        letter-spacing: -0.27px;
        line-height: 48px;
    }
    .slideLis-msg {
        text-align: center;
        min-height: 48px;
        line-height: 48px;
        margin-bottom: 8px;
        font-size: 12px;
        // color: #606060;
        letter-spacing: -0.25px;
        cursor: pointer;
        // background: #000;
    }
    .sliderChildrens {
        height: 40px;
        line-height: 40px;
    }
}
.color60 {
    color: red !important;
}
.color30 {
    color: blue !important;
}
.el-icon-arrow-right:before {
    content: "" !important;
}
.el-collapse-item__header {
    display: block;
    text-align: center;
    font-size: 12px;
    // color: #606060;
}
</style>


