import axios from "axios";
import request from '../../router/axios';
const state = {
  // 暂定的组织管理中的 tree 数据
  organizaTree: [], //组织树数据
  organizaTreeDetail: {}, //组织详情
  menuTree: [], //菜单树 数据
  userList: [], //用户管理，用户列表
  roleList: [], // 角色列表
  userDetail: [],
  rolesMenuTree: [], //当前用户的菜单树
  testval: 0
};
const getters = {
  getOrganizaTreeState(state) {
    return state.organizaTree
  },
  handleroleList(state) {
    return state.roleList
  }
};
const mutations = {
  // 组织树
  getOrganizaTree(state) {
    request({
        url: "/admin/dept/user-tree",
        method: "get",
      })
      .then(res => {
        // console.log(res);
        let datas = res.data.data;
        // console.log(datas);
        state.organizaTree = datas;

      })
      .catch(err => {
        console.log(err);
      });
  },
  // 单个组织详情
  getOrganizaTreeByID(state, id) {
    request({
        url: "/admin/dept/" + id,
        method: "get",
      })
      .then(res => {
        // console.log(res);
        let datas = res.data.data;
        console.log(datas);
        state.organizaTreeDetail = datas;
        // state.organizaTreeDetail.roleNo = datas.roleNo;
        // state.organizaTreeDetail.childNo = datas.childNo;
        // state.organizaTreeDetail.userNo = datas.userNo;
        // state.organizaTreeDetail.existHotel = datas.existHotel;
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 返回对应角色的菜单树
  getMenuTreeByRoleID(state, id) {
    request({
        url: "/admin/menu/tree/" + id,
        method: "get",
      })
      .then(res => {
        console.log(res.data);
        state.rolesMenuTree = res.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 菜单树
  getMenuTree(state) {
    request({
        url: "/admin/menu/tree",
        method: "get",
      })
      .then(res => {
        // console.log(res);
        let datas = res.data.data;
        // console.log(datas);
        state.menuTree = datas;

      })
      .catch(err => {
        console.log(err);
      });
  },
  // 新增组织
  postNewNodeDept(state, obj) {
    request({
      url: '/admin/dept',
      method: 'post',
      data: obj
    }).then(res => {

      console.log(res);
    }).catch(err => {
      console.log(err);
    })
  },
  // 编辑更新node
  putUpdateNodeDept(state, obj) {
    request({
      url: '/admin/dept',
      method: 'put',
      data: obj
    }).then(res => {

      console.log(res);
    }).catch(err => {
      console.log(err);
    })
  },
  // 删除组织
  deleteNodeDept(state, id) {
    request({
      url: '/admin/dept/' + id,
      method: 'delete'
    }).then(res => {

      console.log(res);
    }).catch(err => {
      console.log(err);
    })
  },
  // 用户信息
  getUserPage(state) {
    request({
      url: '/admin/user/page',
      method: 'get'
    }).then(res => {
      // console.log(res.data.data.records);    
      state.userList = res.data.data.records;
    }).catch(err => {
      console.log(err);
    })
  },
  // 根据id 获取用户信息
  getUserByID(state, id) {
    request({
      url: '/admin/user/' + id,
      method: 'get'
    }).then(res => {
      console.log(res.data.data);
      // state.userList =   res.data.data.records;
    }).catch(err => {
      console.log(err);
    })
  },
  //角色 列表
  getRolePage(state) {
    request({
      url: '/admin/role/page',
      method: 'get'
    }).then(res => {
      // console.log(res.data.data.records);
      state.roleList = res.data.data.records;
    }).catch(err => {
      console.log(err);
    })
  },
  // 创建角色
  postNewRole(state, obj) {
    request({
        url: '/admin/role',
        method: 'post',
        data: obj
      }).then(res => {
        console.log(res);

      })
      .catch(err => {
        console.log(err);
      });
  },
  // 修改角色
  putRoleDetail(state, obj) {
    request({
        url: '/admin/role',
        method: 'put',
        data: obj
      }).then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 删除角色
  deleteTheRole(state, id) {
    request({
        url: '/admin/role/' + id,
        method: 'delete',
      }).then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 创建用户
  postCreateUser(state, obj) {
    request({
      url: '/admin/user',
      method: 'post',
      data: obj
    }).then(res => {
      console.log(res);
      // state.roleList =   res.data.data.records;
    }).catch(err => {
      console.log(err);
    })
  },
  // 删除用户
  deleteUser(state, id) {
    request({
      url: '/admin/user/' + id,
      method: 'delete',
    }).then(res => {
      console.log(res);
    }).catch(err => {
      console.log(err);
    })
  },
  // 修改用户信息
  putUpdateUser(state, obj) {
    // console.log(obj);
    request({
      url: '/admin/user',
      method: 'put',
      data: obj
    }).then(res => {

      console.log(res);
    }).catch(err => {
      console.log(err);
    })
  },
};
const actions = {
  // 组织树
  loadOrganizaTree(context) {
    context.commit('getOrganizaTree')
  },
  // 单个组织详情
  loadOrganTreeByID(context, id) {
    context.commit('getOrganizaTreeByID', id)
  },
  // 菜单树
  loadMenuTree(context) {
    context.commit('getMenuTree')
  },
  // 返回角色的菜单树
  loadRolesMenuTree(context, id) {
    context.commit('getMenuTreeByRoleID', id)
  },
  // 添加新组织
  submitNewNodeDept(context, obj) {
    context.commit('postNewNodeDept', obj)
  },
  // 编辑组织
  submitUpdateNodeDept(context, obj) {
    context.commit('putUpdateNodeDept', obj)
  },
  // 删除组织
  submitDeleteNodeDept(context, id) {
    context.commit('deleteNodeDept', id)
  },
  // 获取用户列表
  loadUserlist(context) {
    context.commit('getUserPage')
  },
  // 获取角色列表
  loadRoleList(context) {
    context.commit('getRolePage')
  },
  // 创建角色
  submitNewRole(context, obj) {
    context.commit("postNewRole", obj)
  },
  // 修改角色
  submitRoleDetail(context, obj) {
    context.commit('putRoleDetail', obj)
  },
  // 删除角色
  deleteRole(context, id) {
    context.commit("deleteTheRole", id)
  },
  // 提交创建用户
  submitCreateUser(context, obj) {
    context.commit('postCreateUser', obj)
  },
  submitDeleteUser(context, id) {
    context.commit('deleteUser', id)
  },
  // 修改用户
  submitUpdateUser(context, obj) {
    context.commit('putUpdateUser', obj)
  },
  loadUserByID(context, id) {
    context.commit('getUserByID', id)
  },

}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



