export default {
    name: "mqttws",
    data() {
        return {
            // hostname: "192.168.0.66",
            // port: 8083,
            // clientId:
            //     "2A575554414F4C4F564558555745492A#" + Date.parse(new Date()),
            // timeout: 5,
            // keepAlive: 50,
            // cleanSession: true,
            // ssl: false,
            // topic: "2A575554414F4C4F564558555745492A/+",
            // client: {},
            // options: {},
            // msg: "order",
            // count: 0,
            // timerflag: false
        };
    },
    created: function() {
        let that = this;

        let hotel_token = sessionStorage.getItem("SESSIONACCESSHOTELTOKEN");
        console.log(hotel_token);
        this.client = new Paho.MQTT.Client(
            "192.168.0.66",
            8083,
            hotel_token + "#" + Date.parse(new Date())
        );
        // console.log(this.client);
        var _client = this.client;
        var opt = (this.options = {
            invocationContext: {
                host: "192.168.0.66",
                port: 8083,
                path: _client.path,
                clientId: hotel_token + "#" + Date.parse(new Date())
            },
            timeout: 5,
            keepAliveInterval: 50,
            cleanSession: true,
            useSSL: false,
            onSuccess: function() {
                console.log("onConnected");
                _client.subscribe(hotel_token + "/+"); //订阅主题
                if (that.timerflag == true) {
                    stop();
                }
            },
            onFailure: function(e) {
                console.log(e);
                s = "{time:" + new Date() + ", onFailure()}";
                console.log(s);
                if (that.timerflag == false) {
                    console.log("添加重连定时");
                    that.start();
                }
            }
        });
        this.client.connect(opt); //连接服务器并注册连接成功处理事件
        this.client.onConnectionLost = this.onConnectionLost; //注册连接断开处理事件
        this.client.onMessageArrived = this.onMessageArrived; //注册消息接收处理事件
    },

    methods: {
        ...mapActions("room", ["rcu_messageArrived"]),
        onConnectionLost: function(responseObject) {
            if (responseObject.errorCode !== 0) {
                console.log("onConnectionLost:" + responseObject.errorMessage);
                console.log("连接已断开");
            }
        },
        onMessageArrived: function(message) {
            console.log("收到消息:" + message.payloadString);
            this.rcu_messageArrived(message.payloadString);
        },
        onConnect: function() {
            console.log("onConnected");
            this.client.subscribe("2A575554414F4C4F564558555745492A/+"); //订阅主题
        },
        start: function() {
            let that = this;
            let hostname = "192.168.0.66",
                port = 8083,
                clientId =
                    "2A575554414F4C4F564558555745492A#" +
                    Date.parse(new Date());
            that.timerflag = true;
            window.timer1 = window.setInterval(function() {
                console.log("尝试重连1！");
                that.client = null;
                that.client = new Paho.MQTT.Client(hostname, port, clientId);
                that.client.connect(options);
            }, 5000);
        },
        stop: function() {
            window.clearInterval(window.timer1);
        }
    }
};
