<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <ul class="select-content">
                    <li class="select-li servState">
                        <span>状态</span>
                        <el-select class="servState-input" v-model="value" placeholder="状态">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </li>
                    <li class="select-li servVal">
                        <el-input v-model="searchVal" placeholder="请输入搜索内容"></el-input>
                    </li>
                    <li class="select-li servBTN">
                        <span class="select-li-confirm mg__confirm__btn" @click="serviceSearch">搜索</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="row layer-box room-service">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">服务请求管理</span>
                    <span class="rightTotal">共{{theTableLength}}个服务请求</span>
                </div>
                <div class="layerC-content-service">
                    <div class="auth-msgs-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                            >
                                <el-table-column type="index" label="序号"></el-table-column>
                                <el-table-column prop="room" label="房号"></el-table-column>
                                <el-table-column prop="type" label="服务类型"></el-table-column>
                                <el-table-column prop="sename" label="服务内容"></el-table-column>
                                <el-table-column prop="setime" label="创建时间"></el-table-column>
                                <el-table-column prop="user" label="处理人员"></el-table-column>
                                <el-table-column prop="sestate" label="处理状态"></el-table-column>
                                <el-table-column prop="remind" label="提醒方式"></el-table-column>
                            </el-table>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import moment from "moment";
export default {
    name: "Service",
    data: function() {
        return {
            searchVal: "",
            value: "",
            options: [
                {
                    value: "0",
                    label: "全部"
                },
                {
                    value: "1",
                    label: "设备自身故障"
                },
                {
                    value: "2",
                    label: "送餐服务"
                }
            ],
            tableData: [
                {
                    room: 101,
                    type: "客房1",
                    sename: "送餐",
                    abtype: "送餐服务",
                    sestate: "未处理",
                    remind: "弹窗提醒",
                    setime: "2018-09-12 10:53:00",
                    user: "admin"
                },
                {
                    room: 102,
                    type: "客房2",
                    sename: "送餐",
                    abtype: "送餐服务",
                    sestate: "未处理",
                    remind: "弹窗提醒",
                    setime: "2018-09-12 10:53:00",
                    user: "admin"
                },
                {
                    room: 103,
                    type: "客房3",
                    sename: "送餐",
                    abtype: "送餐服务",
                    sestate: "未处理",
                    remind: "弹窗提醒",
                    setime: "2018-09-12 10:53:00",
                    user: "admin"
                },
                {
                    room: 104,
                    type: "客房4",
                    sename: "送餐",
                    abtype: "送餐服务",
                    sestate: "未处理",
                    remind: "弹窗提醒",
                    setime: "2018-09-12 10:53:00",
                    user: "admin"
                },
                {
                    room: 105,
                    type: "客房5",
                    sename: "送餐",
                    abtype: "送餐服务",
                    sestate: "未处理",
                    remind: "弹窗提醒",
                    setime: "2018-09-12 10:53:00",
                    user: "admin"
                }
            ]
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },
    methods: {
        // 根据状态搜索
        serviceSearch: function() {}
    }
};
</script>
<style lang="less">
.layerC-content-service {
    min-height: 134px;
}
.servState {
    padding: 0 12px;
    width: 20%;
    .servState-input {
        padding: 0 0 0 14px;
        width: 65%;
    }
    input::-webkit-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-moz-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-ms-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
}
.servVal {
    padding: 0 12px;
    width: 70%;
}
.servBTN {
    width: 10%;
    padding: 0 14px 0 12px;
    .select-li-confirm {
        display: block;
        height: 28px;
        line-height: 28px;
    }
}
.room-service {
    background: #fff;
}
.auth-msgs-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }

    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
}
</style>


