<template>
    <div class="mainContR">
        <div class="row layer-box room-organ">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">组织管理</span>
                    <span class="rightTotal">共X个角色</span>
                </div>
                <div class="layerC-content-organ row">
                    <div class="organ-tree col-xs-4 col-sm-4 col-md-4 col-lg-4">
                        <el-tree
                            :data="organizaTree"
                            :props="defaultProps"
                            highlight-current
                            class="organ-el-tree"
                            @node-click="getNodeData"
                            :render-content="renderContent"
                        ></el-tree>
                        <ul class="organ-btn-menu">
                            <li class="organ-btn add" @click="handleAdd">添加组织</li>
                            <li class="organ-btn edit" @click="handleEdit">编辑组织</li>
                            <li class="organ-btn delete" @click="handleDelete">删除</li>
                        </ul>
                    </div>
                    <div class="organ-treehandle col-xs-8 col-sm-8 col-md-8 col-lg-8">
                        <ul class="organ-treehandle-list">
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">
                                    <em class="keyRed">*</em> 父级节点
                                </p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.parentId"
                                        :disabled="formEdit"
                                        placeholder="请输入父级节点"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">节点编号</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.deptId"
                                        placeholder="节点编号"
                                        :disabled="formEdit"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">
                                    <em class="keyRed">*</em>部门名称
                                </p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.organName"
                                        :disabled="formEdit"
                                        placeholder="请输入名称"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">排序</p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.sort"
                                        placeholder="请输入排序"
                                        :disabled="formEdit"
                                        type="number"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row" v-if="organizaTreeDetail.existHotel">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">
                                    <em class="keyRed">*</em>最大组织数
                                </p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.childNo"
                                        :disabled="formEdit"
                                        placeholder="请输入名称"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row" v-if="organizaTreeDetail.existHotel">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">
                                    <em class="keyRed">*</em>最大角色数
                                </p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.roleNo"
                                        :disabled="formEdit"
                                        placeholder="请输入名称"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row" v-if="organizaTreeDetail.existHotel">
                                <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">
                                    <em class="keyRed">*</em>最大用户数
                                </p>
                                <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                    <el-input
                                        class="msg-input"
                                        v-model="treeForm.userNo"
                                        :disabled="formEdit"
                                        placeholder="请输入名称"
                                    ></el-input>
                                </div>
                            </li>
                            <li class="list-li row">
                                <div class="col-xs-11 col-sm-11 col-md-11 col-lg-11 btn-menu">
                                    <span
                                        class="save inputBtn"
                                        v-if="formStatus === 'create'"
                                        @click="handleSave"
                                    >保存</span>
                                    <span
                                        class="update inputBtn"
                                        v-if="formStatus === 'update'"
                                        @click="handleRenew"
                                    >更新</span>
                                    <span
                                        class="cancel inputBtn"
                                        v-if="formStatus != ''"
                                        @click="handleCancel"
                                    >取消</span>
                                </div>
                                <div class="col-xs-1 col-sm-1 col-md-1 col-lg-1"></div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
    name: "Organ",
    data: function() {
        return {
            defaultProps: {
                children: "children",
                label: "name"
            },
            treeForm: {
                parentId: "",
                deptId: "",
                organName: "",
                sort: "",
                childNo: 0,
                roleNo: 0,
                userNo: 0,
                existHotel: 0
            },

            formEdit: true,
            formStatus: "" //update 编辑  create 添加
        };
    },
    computed: {
        ...mapState("dept", ["organizaTree", "organizaTreeDetail"]),        
    },
    methods: {
        ...mapActions("dept", [
            "loadOrganizaTree",
            "submitNewNodeDept",
            "submitUpdateNodeDept",
            "submitDeleteNodeDept",
            "loadOrganTreeByID"
        ]),
        //树状图
        renderContent(h, { node, data, store }) {
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        getNodeData: function(datas) {
            console.log(datas);
            this.treeForm.parentId = datas.parentId;
            this.treeForm.deptId = datas.id;
            this.treeForm.organName = datas.name;
            this.loadOrganTreeByID(datas.id);
            this.treeForm.roleNo = this.organizaTreeDetail.roleNo;
            this.treeForm.childNo = this.organizaTreeDetail.childNo;
            this.treeForm.userNo = this.organizaTreeDetail.userNo;
            // this.formEdit = false;
        },
        // 添加菜单
        handleAdd: function() {
            this.formEdit = false;
            this.formStatus = "create";
            if (this.treeForm.parentId == "") {
                this.treeForm.parentId = 0;
            }
        },
        // 编辑菜单
        handleEdit: function() {
            this.formEdit = false;
            this.formStatus = "update";
        },
        // 删除
        handleDelete: function() {
            if (typeof this.treeForm.deptId !== "number") {
                this.$notify({
                    title: "提示",
                    message: "请正确填写部门名称",
                    duration: 1500,
                    type: "error"
                });
            } else {
                console.log(this.treeForm.deptId);
                this.submitDeleteNodeDept(this.treeForm.deptId);
            }
        },
        // 保存---添加
        handleSave: function() {
            let obj = {};
            // if (typeof this.treeForm.parentId !== "number") {
            //     this.$notify({
            //         title: "错误",
            //         message: "父级节点应为数字！",
            //         duration: 1500,
            //         type: "error"
            //     });
            // } else {

            // }
            if (this.treeForm.organName == "") {
                this.$notify({
                    title: "提示",
                    message: "请正确填写部门名称",
                    duration: 1500,
                    type: "error"
                });
            } else {
                obj.parentId = this.treeForm.parentId;
                obj.name = this.treeForm.organName;
                if (this.treeForm.sort) {
                    obj.sort = this.treeForm.sort;
                }
                let str = JSON.stringify(obj);
                this.submitNewNodeDept(obj);
            }
        },
        // 更新---编辑
        handleRenew: function() {
            let obj = {};
            if (this.treeForm.organName == "") {
                this.$notify({
                    title: "提示",
                    message: "请正确填写部门名称",
                    duration: 1500,
                    type: "error"
                });
            } else {
                obj.parentId = this.treeForm.parentId;
                obj.name = this.treeForm.organName;
                obj.deptId = this.treeForm.deptId;
                obj.childNo = this.treeForm.childNo;
                obj.roleNo = this.treeForm.roleNo;
                obj.userNo = this.treeForm.userNo;
                if (this.treeForm.sort == "") {
                    obj.sort = null;
                } else {
                    obj.sort = this.treeForm.sort;
                }
                let str = JSON.stringify(obj);
                this.submitUpdateNodeDept(obj);
            }
        },
        // 取消
        handleCancel: function() {}
    },
    mounted() {
        this.loadOrganizaTree();
    },
    updated() {}
};
</script>
<style lang="less">
.layerC-content-organ {
    // min-height: 134px;
    // .organ-tree,
    // .organ-treehandle {

    // }
    .organ-tree {
        padding: 24px 0 0 24px;
        .organ-el-tree {
            min-height: 244px;
        }
        .el-icon-caret-right {
            &:before {
                content: "";
            }
        }
        .el-tree-node__content {
            height: 32px;
            .custom-tree-node {
                height: 32px;
                line-height: 32px;
                .chlidIcon {
                    text-align: center;
                }
                .icon--8,
                .icon-close-2 {
                    &:before {
                        color: #999;
                    }
                }
            }
        }
    }
    .organ-treehandle {
        .organ-treehandle-list {
            padding: 24px 50px 0 24px;
            .list-li {
                margin-bottom: 20px;
                height: 28px;
                .name,
                .msg {
                    float: left;
                }
                .name {
                    // width: 92px;
                    height: 28px;
                    text-align: right;
                    line-height: 28px;
                    font-family: PingFangSC-Regular;
                    font-size: 12px;
                    color: #303030;
                    letter-spacing: -0.25px;
                }
                .msg {
                    padding-left: 16px;
                    height: 28px;
                    // width: 80%;
                    .msg-input {
                        .el-input__inner {
                            height: 28px;
                            border: 0 none;
                            background: #f4f4f4;
                            border-radius: 6px;
                        }
                    }
                }
                .btn-menu {
                    .inputBtn {
                        float: right;
                        width: 108px;
                        height: 30px;
                        line-height: 30px;
                        font-family: PingFangSC-Semibold;
                        font-size: 12px;
                        color: #ffffff;
                        letter-spacing: -0.25px;
                        text-align: center;
                        border-radius: 6px;
                        margin: 0 8px 0 0;
                        cursor: pointer;
                    }
                    .cancel {
                        background: #ffa265;
                    }
                    .save,
                    .update {
                        background: #86b3e7;
                    }
                }
            }
        }
    }
}
.room-organ {
    background: #fff;
}
// 操作按钮
.organ-btn-menu {
    height: 72px;
    padding: 24px;
    .organ-btn {
        float: left;
        width: 64px;
        height: 24px;
        margin: 0 8px 0 0;
        border-radius: 6px;
        line-height: 24px;
        text-align: center;
        font-family: PingFangSC-Semibold;
        font-size: 12px;
        color: #ffffff;
        letter-spacing: -0.23px;
        cursor: pointer;
    }
    .organ-btn.add {
        background: #c1e080;
    }
    .organ-btn.edit {
        background: #86b3e7;
    }
    .organ-btn.delete {
        background: #ffa265;
    }
}
</style>


