<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <!-- 替换为搜索内容 -->
                <ul class="select-content">
                    <li class="select-li abType">
                        <el-select v-model="value" placeholder="异常类型">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </li>
                    <li class="select-li abState">
                        <el-select v-model="value" placeholder="状态">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </li>
                    <li class="select-li abVal">
                        <el-input v-model="searchVal" placeholder="请输入搜索内容"></el-input>
                    </li>
                    <li class="select-li abBTN">
                        <span class="select-li-confirm mg__confirm__btn" @click="abnormalSearch">搜索</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="row layer-box room-roominfo">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">客房信息管理</span>
                    <span class="rightTotal">共X个客房信息</span>
                </div>
                <div class="layerC-content-roominfo">
                    <!-- 客房信息列表 -->
                    <div class="abnormal-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                                @selection-change="selectionChange"
                            >
                                <!-- <el-table-column type="selection" width="55"></el-table-column> -->
                                <el-table-column type="index" label="序号" width="48"></el-table-column>
                                <el-table-column prop="room" label="楼宇/层/房间" width="100"></el-table-column>
                                <el-table-column prop="type" label="房间类型" width="100"></el-table-column>
                                <el-table-column prop="online" label="是否在线"></el-table-column>
                                <el-table-column prop="fangtai" label="房态"></el-table-column>
                                <el-table-column prop="dianliang" label="实时电量" width="100"></el-table-column>
                                <el-table-column prop="haveUser" label="是否有人"></el-table-column>
                                
                            </el-table>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Roomsinfo",
    data: function() {
        return {
            tableData: [
                {
                    room: 101,
                    type: "标准",
                    online: "在线",
                    fangtai: "入住故障房",
                    dianliang: "无",
                    haveUser: "有人"
                },
                
            ],
            searchVal: "",
            value: "",
            options: [
                {
                    value: "0",
                    label: "全部"
                },
                {
                    value: "1",
                    label: "设备自身故障"
                },
                {
                    value: "2",
                    label: "预制逻辑异常"
                }
            ]
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },

    methods: {
        // 处理异常
        handleUpdate: function() {
            console.log("异常修改");
        },
        handleDelete: function() {
            console.log("异常删除");
        },
        //表格全选时
        selectionChange: function(row) {
            // row 选择项发生变化时
            console.log(row);
        },
        // 搜索按钮
        abnormalSearch: function() {
            console.log("search");
        }
    }
};
</script>
<style lang="less" >
.layerC-content-roominfo {
    min-height: 134px;
    max-height: 570px;
    overflow: auto;
}
.room-roominfo {
    background: #fff;
}

.abType {
    width: 12%;
    padding: 0 10px;
}
.abState {
    width: 10%;
    padding: 0 10px;
}
.abType,
.abState {
    input::-webkit-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-moz-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-ms-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
}
.abVal {
    padding: 0 10px;
    width: 65%;
    .el-input {
        height: 28px;
        .el-input__inner {
            height: 28px;
            background: #f4f4f4;
            border-radius: 6px;
            border: 0 none;
        }
    }
}
.abBTN {
    width: 12%;
    padding: 0 14px;
    .select-li-confirm {
        display: block;
        height: 28px;
        line-height: 28px;
    }
}
.abnormal-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }

    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
    .element-table-test {
        // checked box 修改颜色
        .el-checkbox__input.is-checked .el-checkbox__inner,
        .el-checkbox__input.is-indeterminate .el-checkbox__inner {
            background-color: #86b3e7;
            border-color: #86b3e7;
        }
        .el-checkbox__inner:hover {
            border-color: #86b3e7;
        }
        .el-checkbox {
            .el-checkbox__inner {
                border-radius: 50%;
            }
        }
    }
}
</style>


