                                <!-- <li class="clearfix control-list-li type-cl">
                                    <p class="devices-name">OUT2A_5</p>
                                    <span class="devices-silderbtn">
                                        <i class="iconBtn icon-cl-1"></i>
                                        <i class="iconBtn icon-cl-3"></i>
                                        <i class="iconBtn icon-cl-2"></i>
                                    </span>
                                    <p class="devices-name">{{valuecl_1}}%</p>
                                    <span class="devices-silderbtn pos">
                                        <el-slider v-model="valuecl_1"></el-slider>
                                    </span>
                                    <p class="devices-name">{{valuecl_bai}}&deg;</p>
                                    <span class="devices-silderbtn angle">
                                        <el-slider v-model="valuecl_bai" :max="valuecl_bai_max"></el-slider>
                                    </span>
                                </li>-->
                            </ul>
                        </el-scrollbar>
                    </div>
                </div>
            </div>
        </div>
        <!-- 异常警告 -->
        <div class="row layer-box room-roominfo">
            <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">X房间</span>
                    <span class="rightTotal">{{rcuToken}}</span>
                </div>
                <div class="layerC-content-roominfo">
                    <!-- 异常警告（处理/未处理  ）列表 -->
