        </div>
        <!-- 场景管理 -->
        <div class="row layer-box room-roominfo">
            <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">场景管理</span>
                    <span class="rightTotal"></span>
                </div>
                <div class="layerC-content-roominfo">
                    <!-- rcu 设备中内置场景的展示，app中可以修改，该页面中只可以常看名称、是否开启 -->
                </div>
