<script>
import axios from "axios";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
export default {
    name: "Roomdetail",
    data: function() {
        return {
            //rcutoken 获取设备
            rcuToken: "",
            sceneDeviceID: "",
            value: "",
            valuecl: 39,
            valuecl_1: 20,
            valuecl_bai: 12,
            valuecl_bai_max: 180,
            value1: 20,
            onoff: false,

            // devicesList: {},
            // devicesListSwitch: {},
            // // alldevices: { pt: false },
            // devicesListTG: {},
            // devicesTypeCL: [], //所有的窗帘的数组
            // devicesListCL: {}, // 所有的窗帘的数据
            datamsg: "success"
        };
    },
    computed: {
        // ...mapState({
        //     alldevices: state => state.room.alldevices
        // })
        ...mapState("room", [
            "alldevices",
            "devicesList",
            "devicesListSwitch",
            "devicesListTG",
            "devicesTypeCL",
            "devicesListCL",
            "devicesTypeWK",
            "devicesListWK",
            "devicesTypeBGM",
            "devicesListBGM"
        ]),
        ...mapState("scene", ["sceneDeviceList"])
    },
    methods: {
        ...mapActions("room", [
            "oneDeviceChange",
            "allDevicesChange",
            "getroomDevicesByRCU"
        ]),
        ...mapActions("scene", ["loadSceneDeviveByRcu", "submitSceneDervice"]),
        testfun: function(com, type) {
            let that = this;
            // let command = com ? "light-on" : "light-off";
            let obj = {
                command: com,
                type: type
            };
            this.allDevicesChange(obj);
        },
        // 单个灯光
        ceshibtn: function(item, ids) {
            let that = this;
            let deviceID = ids;
            let command = item ? "light-on" : "light-off";
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: deviceID,
                command: command
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        // 音量
        bgmVolume: function(e, ids, command) {
            function byte2HexString(byte) {
                let hex = byte.toString(16);
                if (hex.length == 1) {
                    hex = "0" + hex; //加0
                }
                return hex;
            }
            let that = this;
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: command,
                commandData: byte2HexString(e)
            };
            let str = JSON.stringify(json1);
            console.log(str);
            that.oneDeviceChange(str);
        },
        bgmchoose(ids, command) {
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: command
            };
            let str = JSON.stringify(json1);
            console.log(str);
            this.oneDeviceChange(str);
        },
        bgmSwitch(ids, com, type) {
            let json1 = {};
            if (type == "on") {
                let command;
                if (com == 0) {
                    command = "bgm-on";
                } else {
                    command = "bgm-off";
                }
                json1 = {
                    commandType: 0,
                    cmd: 38,
                    id: ids,
                    command: command
                };
            } else {
                let commandData;
                if (com == 0) {
                    commandData = "1";
                } else {
                    commandData = "0";
                }
                json1 = {
                    commandType: 0,
                    cmd: 38,
                    id: ids,
                    command: "bgm-mute",
                    commandData:'0'+ commandData
                };
            }
            let str = JSON.stringify(json1);
            this.oneDeviceChange(str);
            console.log(json1);
        },
        // 温控器
        ceshiwk: function(ids, command, val) {
            function byte2HexString(byte) {
                let hex = byte.toString(16);
                if (hex.length == 1) {
                    hex = "0" + hex; //加0
                }
                return hex;
            }
            let mode = byte2HexString(val);
            let that = this;
            let deviceID = ids;
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: command,
                commandData: mode
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        ceshiwkswh(ids, command) {
            let that = this;
            let deviceID = ids;
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: command
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        // 温控器的加减
        ceshiwktemp(ids, command, data) {
            let that = this;
            let deviceID = ids;
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: command,
                commandData: data
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        // 调光灯
        ceshitgd: function(item, ids) {
            let that = this;
            let deviceID = ids;
            let command = ids == 0 ? "light-off" : "light-move";
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: deviceID,
                command: command,
                commandData: item.toString(16)
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        // 窗帘开关
        ceshicl: function(ids, data) {
            let that = this;
            let deviceID = ids;
            let json1 = {
                commandType: 1,
                cmd: 38,
                id: ids,
                command: data
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },

        // 窗帘滑块
        clslider: function(s, ids, com) {
            let that = this;
            let json1 = {
                commandType: 0,
                cmd: 38,
                id: ids,
                command: com,
                commandData: s.toString(16)
            };
            let str = JSON.stringify(json1);
            that.oneDeviceChange(str);
        },
        getRoomDevices: function() {
            let that = this;
            let access_token = sessionStorage.getItem("SESSIONACCESSTOKEN");
            function hexString2Btye(str) {
                let pos = 0;
                let len = str.length;
                if (len % 2 != 0) {
                    return null;
                }
                len /= 2;
                let hexA = new Array();
                for (let i = 0; i < len; i++) {
                    let s = str.substr(pos, 2);
                    let v = parseInt(s, 16);
                    hexA.push(v);
                    pos += 2;
                }
                // console.log(hexA);
                return hexA;
            }
            axios({
                url: "/data/device/getDevicesByRcuToken",
                headers: {
                    Authorization: "Bearer " + access_token
                },
                method: "get",
                params: {
                    rcuToken: "02171312ffff0202"
                    // rcuToken: this.rcuToken
                }
            })
                .then(res => {
                    console.log(res.data.data);
                    // this.devicesList ;
                    let thedata = res.data.data;

                    let obj = {},
                        switch1value = {};
                    for (let j = 0, len = thedata.length; j < len; j++) {
                        let items = thedata[j];
                        // 创建一个对象，key为 deviceType，value为同类deviceType 组成的数组
                        if (items.deviceType in obj) {
                            obj[items.deviceType].push(items);
                        } else {
                            obj[items.deviceType] = [];
                            obj[items.deviceType].push(items);
                        }
                    }
                    // let str = obj[87][0].statusData;
                    // that.byteString(str);
                    //普通灯 值 对象
                    let objpt = {};
                    for (let name in obj[1]) {
                        let items = obj[1][name];
                        let deviceId_i = items.deviceId;
                        if (items.status == 1) {
                            objpt[deviceId_i] = true;
                        } else {
                            objpt[deviceId_i] = false;
                        }
                    }
                    // console.log(objpt);
                    this.devicesListSwitch = objpt;
                    //调光灯 值 对象
                    let objtg = {};
                    for (let name in obj[2]) {
                        let items = obj[2][name];
                        let deviceId_i = items.deviceId;
                        if (items.status == 1) {
                            objtg[deviceId_i] = parseInt(items.statusData, 16);
                        } else {
                            objtg[deviceId_i] = 0;
                        }
                    }
                    // console.log(objtg);
                    this.devicesListTG = objtg;
                    // 窗帘
                    this.devicesTypeCL = obj[17].concat(obj[29]);
                    let all = this.devicesTypeCL;
                    // console.log(all);
                    let objcl = {};
                    for (let name in all) {
                        let items = all[name];
                        let deviceId_i = items.deviceId;
                        objcl[deviceId_i] = {};
                        // objcl = { decicesID  设备id: { status  开关状态: 0, xc 行程: 00, jd 角度: 00 } };
                        let bytearr = hexString2Btye(items.statusData);
                        objcl[deviceId_i].xc = bytearr[0];
                        objcl[deviceId_i].jd = bytearr[1];
                        if (items.status == 16 || items.status == 19) {
                            objcl[deviceId_i].status = 0;
                        } else if (items.status == 17 || items.status == 20) {
                            objcl[deviceId_i].status = 1;
                        } else {
                            objcl[deviceId_i].status = 2;
                        }
                        if (
                            items.deviceType == 17 ||
                            items.deviceType == 18 ||
                            items.deviceType == 23
                        ) {
                            objcl[deviceId_i].type = 1;
                        } else if (
                            items.deviceType == 28 ||
                            items.deviceType == 19 ||
                            items.deviceType == 29
                        ) {
                            objcl[deviceId_i].type = 3;
                        } else {
                            objcl[deviceId_i].type = 2;
                        }
                    }
                    // console.log(objcl);

                    this.devicesListCL = objcl;
                    // console.log(obj);
                    this.devicesList = obj;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        byteString: function(obj) {
            function isStringNull(str) {
                if (!(typeof str != "undefined" && str != "" && str != null)) {
                    return true;
                }
                return false;
            }

            function hexString2Btye(str) {
                if (isStringNull(str)) {
                    return null;
                }

                let pos = 0;
                let len = str.length;
                if (len % 2 != 0) {
                    return null;
                }
                len /= 2;
                let hexA = new Array();
                for (let i = 0; i < len; i++) {
                    let s = str.substr(pos, 2);
                    let v = parseInt(s, 16);
                    hexA.push(v);
                    pos += 2;
                }

                console.log(hexA);
                return hexA;
            }
            hexString2Btye("3a");

            let version = 0;
            let stateValues = hexString2Btye("3a");
            if (version >= 0x45) {
                if (stateValues.length >= 6) {
                    let modeByte = stateValues[4];
                    let temp = stateValues[1] / 2;
                    let speed = stateValues[5];
                }
            } else {
                let modeByte = (stateValues[0] >> 5) & 0x7;
                let speed = (stateValues[0] >> 3) & 0x3;
                let temp = stateValues[1] / 2;
                console.log(modeByte, speed, temp);
            }
            function bytes2HexString(bytes) {
                let hexs = "";
                for (let i = 0; i < bytes.length; i++) {
                    let hex = bytes[i].toString(16);
                    if (hex.length == 1) {
                        hex = "0" + hex;
                    }
                    hexs += hex.toUpperCase();
                }
                console.log(hexs);
                return hexs;
            }
            function littleEndianString2Int(str) {
                let bytes = hexString2Btye(str);

                if (bytes == null) {
                    return -1;
                }

                if (bytes.length == 0) {
                    return -1;
                }

                let value = 0;
                let count = bytes.length;

                for (let aval = count - 1; aval >= 0; aval--) {
                    value += bytes[aval] << (8 * aval);
                }
                // console.log(value);
                return value;
            }
        },
        // 房间默认场景导入
        handleSceneConfirm: function() {
            if (
                this.sceneDeviceID == "" ||
                this.sceneDeviceID == null ||
                this.sceneDeviceID == undefined
            ) {
                this.$notify({
                    title: "提示",
                    message: "请选择正确的场景！",
                    duration: 1500,
                    type: "error"
                });
            } else {
                console.log(this.sceneDeviceID);
                let json1 = {
                    commandType: 1,
                    cmd: 38,
                    commandData: this.sceneDeviceID
                };
                let obj = JSON.stringify(json1);
                this.submitSceneDervice(obj);
            }
        }
    },

    created() {
        // this.rcuToken = this.$route.params.rcuToken;
    },
    mounted() {
        let obj = {};
        obj.rcuToken = "02171312ffff0202";
        this.getroomDevicesByRCU();
        this.loadSceneDeviveByRcu(obj);
        // this.getRoomDevices();
        // this.byteString();
    },
    updated() {
        // console.log($("#slider"));
        $("#slider").roundSlider({
            radius: 140,
            width: 12,
            circleShape: "half-top",
            sliderType: "min-range",
            lineCap: "round",
            showTooltip: true,
            value: 50,
            handleSize: 32,
            min: 16,
            max: 32,
            change: function(e) {
                console.log(e);
            }
        });
    }
};
</script>
<style lang='less'>
.layerC-content-roominfo {
    min-height: 134px;
}
.room-roominfo {
    background: #fff;
}
@media (max-width: 991px) {
    .room-devices-box {
        .devices-lists {
            &:nth-child(even) {
                padding-left: 0;
            }
            &:nth-child(odd) {
                padding-right: 0;
            }
        }
    }
}
</style>


