                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        hoverAnimation: false,
                        silent: true,
                        minAngle: 15,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#FAD650", "#EEEEEE"],

