                        startAngle: 270,
                        hoverAnimation: false,
                        silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#FFA265", "#EEEEEE"],

                        data: [
                            { value: this.roomdata.filthyCount, name: "脏乱" },
                            {
                                value:
                                    this.roomdata.allCount -
                                    this.roomdata.filthyCount,
                                name: ""
                            }
                        ]
                    }
                ]
            },
            option5 = {
                title: {
                    text: this.roomdata.faultCount,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        minAngle: 15,
                        hoverAnimation: false,
                        silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#9275F7", "#EEEEEE"],

                        data: [
                            { value: this.roomdata.faultCount, name: "故障" },
                            {
                                value:
                                    this.roomdata.allCount -
                                    this.roomdata.faultCount,
                                name: ""
                            }
                        ]
                    }
                ]
            },
            option6 = {
                title: {
                    text: this.roomdata.allCount,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        minAngle: 15,
                        hoverAnimation: false,
                        silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
