        // myChart4.setOption(option4);
        // myChart5.setOption(option5);
        // myChart6.setOption(option6);
        //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
        window.addEventListener("resize", function() {
            myChart1.resize();
            myChart2.resize();
            myChart3.resize();
            myChart4.resize();
            myChart5.resize();
            myChart6.resize();
        });

        $(".btnn").on("click", function() {
            if ($(this).attr("isopen") == "false") {
                $(this)
                    .stop()
                    .attr("isopen", "true")
                    .animate({ left: "12px" });
                $(this)
                    .parent()
                    .css("background-color", "#FAD650");
                // console.log("开");
            } else {
                $(this)
                    .stop()
                    .attr("isopen", "false")
                    .animate({ left: "0px" });
                $(this)
                    .parent()
                    .css("background-color", "#838383");
                // console.log("关");
            }
