<style lang="less" scoped>
.right {
    width: 30px;
    height: 12px;
    padding: 3px;
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    background-color: #838383;
    position: relative;
}

.right .btnn {
    width: 18px;
    height: 18px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: -3px;
    left: 0px;
}
// @import "../../../static/css/mainContR.css";
.test-top {
    height: 120px;
}
.testBTN {
    width: 100px;
    height: 20px;
    background: #0cf;
}
.testBTN2 {
    width: 100px;
    height: 20px;
    background: rgb(55, 0, 255);
}
.testBTN3 {
    width: 100px;
    height: 20px;
    background: rgb(255, 0, 76);
}

.theimg {
    min-width: 10px;
    min-height: 10px;
}
// echart数据表
.room-chart-list {
    padding-bottom: 20px;
    .chart-list-li {
        height: 140px;
        padding: 0 20px;
        .list-rz,
        .list-kj,
        .list-qj,
        .list-zl,
        .list-gz,
        .list-all {
            height: 100px;
        }
        .list-roommsg {
            height: 20px;
            font-family: PingFangSC-Regular;
            font-size: 11px;
            color: #303030;
            letter-spacing: -0.23px;
            text-align: center;
        }
    }
}
.rpage-header {
    background: #fff;
    height: 56px;
    /* border-radius: 8px; */
    /* margin-bottom: 24px; */
}

.element-table-test {
    // padding-bottom: 20px;
    height: 200px;
}
.testbox {
    width: 700px;
}
.scroll-test-box {
    height: 280px;
    padding: 10px;
    background: red;
    .scroll-test-box-cont {
        height: 500px;
        width: 800px;
        background: #ff0;
    }
}
.rroom-info {
    min-height: 182px;
    .room-jk {
        min-height: 182px;
        padding-right: 24px;
        .roomtalbe {
            background: #fff;
            min-height: 182px;
            border-radius: 8px;
        }
    }
    .room-sq {
        min-height: 182px;
        background: #fff;
        border-radius: 8px;
    }
}
.layerC-content-ser {
    min-height: 134px;
}
.room-ser {
    background: #fff;
    // max-height: 300px;
}
// 表格
// .service-msgs-table {
//     // max-height: 300px;
//     padding: 0 12px 18px;
//     height: 300px;
//     // width: 600px;
// }

.service-msgs-table {
    // max-height: 300px;
    padding: 0 12px 18px;
    height: 300px;
    overflow: auto;
    .el-table {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        // 表格头
        .el-table__header {
            th.is-leaf {
                border: 0 none;
            }
            thead {
                color: #303030;
                letter-spacing: -0.25px;
                font-weight: 500;
            }
            th {
                font-weight: 500;
            }
        }
        // 表格body 测试用 测试用 测试用
        .el-table__body {
            tbody tr {
                // border-bottom:1px solid #f00;
                &:nth-child(odd) {
                    background: #fefefe;
                }
                &:nth-child(even) {
                    background: #fafafa;
                }
            }
        }
    }
    .el-table:before {
        height: 0;
    }

    .el-table td,
    .el-table th.is-leaf {
        // border: 0 none;
    }
    // hover 时 正行的 颜色改变
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #eee !important;
    }
}
</style>     


