<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box scene-header">
            <div class="col-sm-12 col-md-12 col-lg-12 scene-header-create">
                <div class="scene-number">当前场景数量</div>
                <div class="scene-create">
                    <router-link to="createScene">新建场景</router-link>
                </div>
            </div>
        </div>

        <div class="row layer-box-scene scene-control">
            <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <ul class="theSceneList row">
                    <!-- <li class="sceneList-li col-xs-5 col-sm-4 col-md-3 col-lg-3">
                        <div class="li-box"></div>
                    </li>
                    <li class="sceneList-li col-xs-5 col-sm-4 col-md-3 col-lg-3">
                        <div class="li-box"></div>
                    </li>
                    <li class="sceneList-li col-xs-5 col-sm-4 col-md-3 col-lg-3">
                        <div class="li-box"></div>
                    </li>
                    <li class="sceneList-li col-xs-5 col-sm-4 col-md-3 col-lg-3">
                        <div class="li-box"></div>
                    </li>
                    <li class="sceneList-li col-xs-5 col-sm-4 col-md-3 col-lg-3">
                        <div class="li-box"></div>
                    </li>-->
                    <li class="sceneList-li">
                        <ul class="li-box">
                            <li class="li-box-name title-font">睡眠模式</li>
                            <li class="li-box-switch">
                                <span>定时开关</span>
                                <el-switch
                                    v-model="theswitch"
                                    class="the-switch-input"
                                    active-color="#FAD650"
                                    inactive-color="#EAEAEA"
                                ></el-switch>
                            </li>
                            <li class="li-box-btn">
                                <span class="theBTN-turn mg__confirm__btn">启动</span>
                                <span class="theBTN icon-2x8"></span>
                                <span class="theBTN icon-2x12"></span>
                            </li>
                        </ul>
                    </li>
                    <!-- <li class="sceneList-li">
                        <ul class="li-box"></ul>
                    </li>
                    <li class="sceneList-li">
                        <ul class="li-box"></ul>
                    </li>
                    <li class="sceneList-li">
                        <ul class="li-box"></ul>
                    </li>
                    <li class="sceneList-li">
                        <ul class="li-box"></ul>
                    </li>-->
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Scene",
    data: function() {
        return {
            theswitch: "0"
        };
    },
    computed: {},
    methods: {},
    mounted() {}
};
</script>
<style lang="less">
.layerC-content-service {
    min-height: 134px;
}
// 头部 新建
.scene-header {
    padding: 14px 0 0 24px;
    .scene-header-create {
        height: 28px;
        display: flex;
        line-height: 28px;
        .scene-number {
            width: 150px;
        }
        .scene-create {
            width: 86px;
            height: 28px;
            background: #86b3e7;
            border-radius: 6px;
            font-family: PingFangSC-Semibold;
            font-size: 11px;
            color: #ffffff;
            letter-spacing: -0.23px;
            text-align: center;
            cursor: pointer;
            margin-left: 40px;
            a {
                color: #ffffff;
            }
            // line-height: 28px;
        }
    }
}
.scene-control {
    // background: #fff;
}
.layer-box-scene {
}
.theSceneList {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    /* 这里需要修改 已存在的场景列表需要响应式布局 未实现 */
    .sceneList-li {
        // height: 150px;
        // margin-bottom: 24px;
        // .li-box {
        //     background: #ffffff;
        //     box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.05);
        //     border-radius: 8px;
        //     height: 150px;
        // }
        // &:nth-child(odd) {
        //     padding-right: 24px;
        // }
        // &:nth-child(even) {
        //     padding-left: 24px;
        // }
        background: #ffffff;
        box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.05);
        border-radius: 8px;
        min-height: 150px;
        width: 240px;
        margin: 0 24px 24px 0;

        .li-box {
            padding: 0 24px;
            .li-box-name {
                min-height: 48px;
                padding: 16px 0 0;
                font-weight: 700;
            }
            .li-box-switch {
                height: 42px;
                padding: 16px 0 0;
                display: flex;
                justify-content: space-around;
                .the-switch-input {
                    width: 30px;
                    height: 10px;

                    .el-switch__core {
                        width: 30px;
                        height: 10px;

                        &:after {
                            top: -4px;
                            background: #ffffff;
                            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
                        }
                    }
                }
            }
            .li-box-btn {
                height: 60px;
                padding: 16px 0 0;
                .theBTN-turn {
                    float: left;
                    width: 118px;
                    height: 24px;
                    line-height: 24px;
                    font-size: 12px;
                    margin-right: 8px;
                    background: #c1e080;
                }
                .theBTN {
                    float: left;
                    width: 24px;
                    height: 24px;
                    font-size: 24px;
                    margin-right: 6px;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>


