import axios from "axios";
import request from '../../router/axios';
const state = {
  // 暂定的组织管理中的 tree 数据
  sceneDeviceList: [], //组织树数据

};
const getters = {
  getSceneDeviceList(state) {
    return state.sceneDeviceList
  },
};
const mutations = {
  // rcu内置的场景
  getSceneDeviceListByRCU(state, obj) {
    request({
        // 02171312ffff0202
        // 02171312ffff0208
        url: "/data/tscenedevice/page?rcuToken=" + obj.rcuToken,
        method: "get",
      })
      .then(res => {
        // console.log(res);
        state.sceneDeviceList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 导入默认的场景
  getControlSceneDervice(state, obj) {
    request({
        url: "data/tscenedevice/controlSceneService",
        method: "get",
        params: {
          rcuToken: "02171312ffff0202",
          jsonCommand: obj
        }

      })
      .then(res => {
        console.log(res);
        state.sceneDeviceList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
const actions = {
  // 获得默认场景列表
  loadSceneDeviveByRcu(context, obj) {
    context.commit('getSceneDeviceListByRCU', obj)
  },
  // 导入默认场景
  submitSceneDervice(context, obj) {
    context.commit('getControlSceneDervice', obj)
  }

}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



