<template>
    <div class="mainContR">
        <div class="row layer-box room-user">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">用户管理</span>
                    <span class="rightTotal">共X个用户</span>
                </div>
                <!-- <div class="layerC-content-user">
                   
                </div>-->
                <div class="user-msgs-table layerC-content-user">
                    <template>
                        <el-table
                            :data="userList"
                            style="width: 100%"
                            class="element-table-test"
                            height="260"
                        >
                            <el-table-column prop="userId" label="序号" width="70"></el-table-column>
                            <el-table-column prop="username" label="用户名" width="150"></el-table-column>
                            <el-table-column prop="phone" label="手机号" width="130"></el-table-column>
                            <el-table-column prop="room" label="角色" width="150">
                                <template slot-scope="scope">
                                    <el-tag
                                        v-for="(item,index) in scope.row.roleList"
                                        :key="index"
                                        effect="plain"
                                        class="tableTag"
                                    >{{ item.roleName }}</el-tag>
                                </template>
                            </el-table-column>
                            <el-table-column prop="deptName" label="部门" width="150"></el-table-column>
                            <el-table-column prop="room" label="状态" width="90"></el-table-column>
                            <el-table-column
                                prop="createTime"
                                label="创建时间"
                                width="130"
                                :formatter="formatDate"
                            ></el-table-column>
                            <el-table-column prop="type" fixed="right" label="操作" width="150">
                                <template slot-scope="scope">
                                    <div
                                        @click="handleUpdate(scope.$index, scope.row)"
                                        class="updata tableBtn icon-2x8"
                                    ></div>
                                    <div
                                        @click="handleDelete(scope.$index, scope.row)"
                                        class="delete tableBtn icon-2x12"
                                    ></div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
                <ul class="user-table-bottom row">
                    <li class="add-user col-xs-2 col-sm-2 col-md-2 col-lg-2">
                        <button class="add-user-btn" @click="dialogShow">添加用户</button>
                        <el-dialog
                            class="create-user-form"
                            :title="dialogData.title"
                            :visible.sync="dialogFormVisible"
                            center
                            :before-close="handleClose"
                        >
                            <ul class="form-list">
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">用户名称</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            v-model="theUser.username"
                                            placeholder="请输入用户名称"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">密码设置</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            placeholder="请输入密码"
                                            v-model="theUser.password"
                                            type="password"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">所属组织</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            placeholder="请选择所属组织"
                                            v-model="deptName"
                                            v-on="{focus:codefocus,blur:codeblur}"
                                        ></el-input>
                                        <el-dialog
                                            class="choose-organ-dialog"
                                            title="请选择"
                                            :visible.sync="dialogFormVisiblezz"
                                            center
                                            append-to-body
                                        >
                                            <div>
                                                <el-tree
                                                    :data="organizaTree"
                                                    :props="defaultProps"
                                                    highlight-current
                                                    class="organ-el-tree"
                                                    @node-click="getNodeData"
                                                    :render-content="renderContent"
                                                ></el-tree>
                                            </div>
                                            <div slot="footer" class="dialog-footer">
                                                <el-button @click="dialogFormVisiblezz = false">取 消</el-button>
                                                <el-button
                                                    type="primary"
                                                    @click="organDialogConfirm"
                                                >确 定</el-button>
                                            </div>
                                        </el-dialog>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">手机号码</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input thenumber"
                                            placeholder="请输入手机号码"
                                            v-model="theUser.phone"
                                            @change="handchange"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">角色</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-select
                                            v-model="theUser.role"
                                            multiple
                                            placeholder="请选择"
                                            class="msg-input"
                                        >
                                            <el-option
                                                v-for="item in roleList"
                                                :key="item.roleId"
                                                :label="item.roleName"
                                                :value="item.roleId"
                                            ></el-option>
                                        </el-select>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">状态</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <!-- <el-input
                                            class="msg-input"
                                            placeholder="请选择状态"
                                            v-model="theUser.status"
                                        ></el-input>-->
                                        <el-select
                                            v-model="theUser.lockFlag"
                                            placeholder="请选择状态"
                                            class="msg-input"
                                        >
                                            <el-option
                                                v-for="item in statusList"
                                                :key="item.status"
                                                :label="item.label"
                                                :value="item.status"
                                            ></el-option>
                                        </el-select>
                                    </div>
                                </li>
                            </ul>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="handleCancel">取 消</el-button>
                                <el-button type="primary" @click="handleConfirm">确 定</el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="talbe-paging col-xs-10 col-sm-10 col-md-10 col-lg-10">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage4"
                            :page-sizes="[100, 200, 300, 400]"
                            :page-size="100"
                            layout=" prev, pager, next, jumper, sizes"
                            :total="400"
                        ></el-pagination>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import moment from "moment";
export default {
    name: "Nonadmin",
    data: function() {
        return {
            statusList: [
                {
                    status: "0",
                    label: "有效"
                },
                {
                    status: "9",
                    label: "锁定"
                }
            ],
            defaultProps: {
                children: "children",
                label: "name"
            },
            dialogFormVisible: false,
            dialogFormVisiblezz: false,
            currentPage4: 4,
            theUser: {
                username: "",
                password: "",
                organ: "",
                organID: "",
                phone: "",
                role: [],
                status: "",
                lockFlag: "",
                restaurants: []
            },
            deptId: "",
            deptName: "",
            // 弹框数据
            dialogData: {
                title: "",
                type: "" //create 新增用户  update 更新
            },
            // 当前用户
            currentUser: {},
            currentRoleList: []
        };
    },
    computed: {
        ...mapState("dept", [
            "userList",
            "roleList",
            "menuTree",
            "organizaTree"
        ])
    },
    methods: {
        ...mapActions("dept", [
            "loadUserlist",
            "loadOrganizaTree",
            "loadMenuTree",
            "loadRoleList",
            "submitCreateUser",
            "submitDeleteUser",
            "submitUpdateUser",
            "loadUserByID"
        ]),
        handleUpdate: function(index, row) {
            console.log("修改用户");
            if (row.roleList.length > 0) {
                for (let items of row.roleList) {
                    this.theUser.role.push(items.roleId);
                }
            }
            this.dialogData.title = "编辑用户";
            this.dialogData.type = "updeate";
            this.theUser.username = row.username;
            this.deptName = row.deptName;
            this.deptId = row.deptId;
            this.theUser.phone = row.phone;
            this.theUser.lockFlag = row.lockFlag;
            this.currentUser = row;
            this.dialogFormVisible = true;
            // console.log(this.userList[index]);
            // this.submitUpdateUser(str)
        },
        handleDelete: function(index, row) {
            // console.log(index);
            // console.log(row);
            let ids = row.userId;
            console.log(ids);
            this.submitDeleteUser(ids);
        },
        formatDate: function(date) {
            return moment(date).format("YYYY-MM-DD");
        },
        handleSizeChange: function(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange: function(val) {
            console.log(`当前页: ${val}`);
        },
        handchange: function() {
            console.log(this.theUser.phone);
            this.theUser.phone = this.theUser.phone.replace(/[^0-9]/g, "");
        },
        // 组织的弹窗 确认按钮
        organDialogConfirm: function() {
            this.dialogFormVisiblezz = false;
            // console.log(this.deptName);
        },
        //树状图
        renderContent: function(h, { node, data, store }) {
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        getNodeData: function(datas) {
            console.log(datas);
            this.deptName = datas.name;
            this.deptId = datas.id;
        },
        codefocus: function() {
            this.dialogFormVisiblezz = !this.dialogFormVisiblezz;
        },
        codeblur: function() {},

        handleCancel: function() {
            this.dialogFormVisible = false;
            this.dialogFormdataEmpty();
        },
        // 创建用户 OR 编辑用户 确认
        handleConfirm: function() {
            if (this.dialogData.type === "create") {
                //添加用户
                let obj = {};
                if (this.theUser.username.trim() == "") {
                    this.$notify({
                        title: "错误",
                        message: "用户名不能为空！",
                        type: "error"
                    });
                } else if (this.theUser.username.length < 4) {
                    this.$notify({
                        title: "错误",
                        message: "用户名不能少于4个字符！",
                        type: "error"
                    });
                } else if (this.theUser.password.trim() == "") {
                    this.$notify({
                        title: "错误",
                        message: "密码不能为空！",
                        type: "error"
                    });
                } else if (this.theUser.password.trim().length < 4) {
                    this.$notify({
                        title: "错误",
                        message: "密码不能少于4个字符！",
                        type: "error"
                    });
                } else if (this.deptId == 0 || this.deptId == "") {
                    this.$notify({
                        title: "错误",
                        message: "请选择正确的组织！",
                        type: "error"
                    });
                } else if (this.theUser.role.length < 1) {
                    this.$notify({
                        title: "错误",
                        message: "请至少选择一个角色！",
                        type: "error"
                    });
                } else {
                    obj.deptId = this.deptId;
                    obj.deptName = "";
                    obj.lockFlag = this.theUser.lockFlag;
                    obj.password = this.theUser.password;
                    obj.phone = this.theUser.phone;
                    obj.role =
                        this.theUser.role.lenght == 0 ? [] : this.theUser.role;
                    obj.username = this.theUser.username.trim();
                    console.log(obj);
                    this.dialogFormVisible = false;
                    this.submitCreateUser(obj).then(() =>{
                        console.log("成功");
                    }).catch(()=>{
                        console.log("失败");
                    })
                }
            } else {
                let nowDate = moment().format("YYYY-MM-DD HH:mm:ss");
                let obj = {};
                if (this.theUser.password != "") {
                    obj.password = this.theUser.password;
                }

                obj.delFlag = this.currentUser.delFlag;
                obj.deptId = this.deptId;
                obj.lockFlag = this.theUser.lockFlag;
                obj.phone = this.theUser.phone;
                obj.role = this.theUser.role;
                obj.updateTime = nowDate;
                obj.userId = this.currentUser.userId;
                obj.username = this.theUser.username;
                console.log(obj);
                this.submitUpdateUser(obj);
            }
        },
        // 显示弹框 添加用户
        dialogShow: function() {
            console.log("添加用户");
            this.dialogData.title = "添加用户";
            this.dialogFormVisible = true;
            this.dialogData.type = "create";
        },
        // 非按钮 关闭 dialog
        handleClose: function() {
            this.dialogFormVisible = false;
            this.dialogFormdataEmpty();
        },
        // 清空数据
        dialogFormdataEmpty: function() {
            this.theUser.username = "";
            this.theUser.password = "";
            this.theUser.organ = "";
            this.theUser.organID = "";
            this.theUser.phone = "";
            this.theUser.role = [];
            this.theUser.status = "";
            this.theUser.lockFlag = "";
            this.theUser.restaurants = [];
            this.deptId = "";
            this.deptName = "";
        }
    },

    mounted() {
        this.loadUserlist();
        this.loadOrganizaTree();
        this.loadMenuTree();
        this.loadRoleList();
    }
};
</script>

<style lang="less" >
.layerC-content-user {
    min-height: 134px;
}
.room-user {
    background: #fff;
}
.user-msgs-table {
    // max-height: 300px;
    padding: 0 12px 18px;
    height: 300px;
    overflow: auto;
    .el-table {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        // 表格头
        .el-table__header {
            th.is-leaf {
                border: 0 none;
            }
            thead {
                color: #303030;
                letter-spacing: -0.25px;
                font-weight: 500;
            }
            th {
                font-weight: 500;
            }
        }
        // 表格body
        .el-table__body {
            tbody tr {
                // border-bottom:1px solid #f00;
                &:nth-child(odd) {
                    background: #fefefe;
                }
                &:nth-child(even) {
                    background: #fafafa;
                }
            }
        }
    }
    .el-table:before {
        height: 0;
    }

    .el-table td,
    .el-table th.is-leaf {
        // border: 0 none;
    }
    // hover 时 正行的 颜色改变
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #eee !important;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
}
.user-table-bottom {
    height: 60px;
    padding: 12px 12px 24px 12px;
    margin-top: 20px;
    .add-user,
    .talbe-paging {
        height: 24px;
    }
    .add-user {
        .add-user-btn {
            height: 24px;
            font-family: PingFangSC-Semibold;
            font-size: 12px;
            color: #ffffff;
            background: #86b3e7;
            letter-spacing: -0.23px;
            text-align: center;
            padding: 0 12px;
            line-height: 24px;
            border-radius: 6px;
        }
    }
}
.create-user-form {
    .el-dialog {
        // dialog 标题
        .el-dialog__header {
            .el-dialog__title {
                font-family: PingFangSC-Semibold;
                font-size: 14px;
                color: #303030;
                letter-spacing: -0.25px;
                text-align: center;
                font-weight: bolder;
            }
        }
    }
    .form-list {
        .form-list-li {
            margin-bottom: 20px;
            height: 28px;
            .name,
            .msg {
                float: left;
            }
            .name {
                // width: 92px;
                height: 28px;
                text-align: right;
                line-height: 28px;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #303030;
                letter-spacing: -0.25px;
            }
            .msg {
                padding-left: 16px;
                height: 28px;
                // width: 80%;
                .el-autocomplete {
                    width: 100%;
                }
                .el-select {
                    width: 100%;
                }
                .msg-input {
                    //  width: 100%;
                    .el-input__inner {
                        height: 28px;
                        border: 0 none;
                        background: #f4f4f4;
                        border-radius: 6px;
                    }

                    .el-input__suffix {
                        height: 28px;

                        .el-input__icon {
                            line-height: 28px;
                        }
                    }
                }
            }
        }
    }
}
.choose-organ-dialog {
}
</style>


