
<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <!-- 替换为搜索内容 -->
                <ul class="select-content">
                    <li class="select-li sugState">
                        <span>状态</span>
                        <el-select class="sugState-input" v-model="value" placeholder="状态">
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </li>
                    <li class="select-li sugVal">
                        <el-input v-model="searchVal" placeholder="请输入要查询的手机号"></el-input>
                    </li>
                    <li class="select-li sugBTN">
                        <span class="select-li-confirm mg__confirm__btn" @click="abnormalSearch">搜索</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="row layer-box room-guest">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">客户信息管理</span>
                    <span class="rightTotal">共X个客户信息</span>
                </div>
                <div class="layerC-content-guest">
                    <!-- 客人身份信息列表 -->
                    <div class="auth-msgs-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                            >
                                <el-table-column type="index" label="序号" width="200"></el-table-column>
                                <el-table-column prop="room" label="用户名"></el-table-column>
                                <el-table-column prop="room" label="手机号码"></el-table-column>
                                <el-table-column prop="room" label="身份证号码"></el-table-column>
                                <el-table-column prop="room" label="性别"></el-table-column>
                                <el-table-column prop="room" label="状态"></el-table-column>
                                <!-- <el-table-column prop="asd" fixed="right" label="操作" width="150">
                                    <template slot-scope="scope">
                                        <div
                                            @click="handleUpdate(scope.$index, scope.row)"
                                            class="updata tableBtn icon-2x8"
                                        ></div>
                                    </template>
                                </el-table-column>-->
                            </el-table>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Guest",
    data: function() {
        return {
            tableData: [
                {
                    room: 101,
                    hotel: "如家",
                    reqname: "aaaa",
                    phone: "123456789",
                    resname: "admin",
                    tousuTime: "2018-08-16 19:32:58",
                    huifuTime: "2018-08-19 11:22:33",
                    type: "投诉"
                }
            ],
            searchVal: "",
            value: "",
            options: [
                {
                    value: "0",
                    label: "全部"
                },
                {
                    value: "1",
                    label: "设备自身故障"
                },
                {
                    value: "2",
                    label: "预制逻辑异常"
                }
            ]
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },
    methods: {
        // 搜索按钮
        abnormalSearch: function() {
            console.log("search");
        },
        //表格全选时
        selectionChange: function(row) {
            // row 选择项发生变化时
            console.log(row);
        },
        // 处理异常
        handleUpdate: function() {
            console.log("修改");
        },
        handleDelete: function() {
            console.log("删除");
        }
    }
};
</script>
<style lang="less">
.layerC-content-guest {
    min-height: 134px;
}
.room-guest {
    background: #fff;
}
.sugState {
    padding: 0 12px;
    width: 20%;
    .sugState-input {
        padding: 0 0 0 14px;
        width: 65%;
    }
    input::-webkit-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-moz-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
    input:-ms-input-placeholder {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        text-align: center;
    }
}
.sugVal {
    padding: 0 12px;
    width: 70%;
}
.sugBTN {
    width: 10%;
    padding: 0 14px 0 12px;
    .select-li-confirm {
        display: block;
        height: 28px;
        line-height: 28px;
    }
}
.auth-msgs-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
}
</style>


