import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MainSlider from '../components/mainSlider/mainSlider.vue';
import VuetansSlier from '../components/mainSlider/vuetansSlier.vue';
import RoomIndex from '../components/roomIndex/roomIndex.vue';
import Scene from "../components/opermgt/scene.vue";
import Newscene from '../components/opermgt/createScene.vue';
import Authorization from '../components/opermgt/authorization.vue';
import Abnormal from '../components/opermgt/abnormalalarm.vue';
import Service from '../components/opermgt/servicerequest.vue';
import Suggest from '../components/opermgt/suggest.vue';
import TheHomePage from '../components/theHomePage/theHomePage.vue';
import Buildings from '../components/hotelInfo/buildings.vue';
import Storeys from '../components/hotelInfo/storeys.vue';
import Roomtype from '../components/hotelInfo/roomType.vue';
import Roomsinfo from '../components/hotelInfo/roomsInfo.vue';
import Roomarea from '../components/hotelInfo/roomAreaInfo.vue';
import Device from '../components/hotelInfo/devices.vue';
import Devicetype from '../components/hotelInfo/deviceType.vue';
import Energy from '../components/statistics/engertable.vue';
import Guest from '../components/statistics/guest.vue';
import Checkin from '../components/statistics/checkin.vue';
import Themenu from '../components/account/juris.vue';
import Nonadmin from '../components/account/user.vue';
import Backup from '../components/database/backup.vue';
import Restore from '../components/database/restore.vue';
import Timer from '../components/database/timer.vue';
import Rcuhost from '../components/system/rcuhost.vue';
import Theconfig from '../components/system/theconfig.vue';
import Exception from '../components/system/abnormalreminder.vue';
import Servtype from '../components/system/servicetype.vue';
import Hotellog from '../components/system/hotellog.vue';
import Login from '../components/login/login.vue';
import Roomdetail from '../components/roomIndex/roomDetail.vue';
import Organ from '../components/account/organiza.vue';
import Role from '../components/account/role.vue';
import Createhotel from '../components/syshotel/createHotel.vue';
//测试页面
import Testpage from '../components/testpage.vue';

Vue.use(Router);
export default new Router({
  routes: [{
      path: '/theHomePage',
      name: 'TheHomePage',
      component: TheHomePage
    },
    {
      path: '/roomIndex',
      name: 'RoomIndex',
      component: RoomIndex
    },
    {
      path: '/roomIndex/roomDetail/:id',
      name: 'Roomdetail',
      component: Roomdetail
    },
    {
      path: '/oper/scene',
      name: 'Scene',
      component: Scene
    },
    {
      path: '/oper/createScene',
      name: 'Newscene',
      component: Newscene
    },
    {
      path: '/oper/authorization',
      name: 'Authorization',
      component: Authorization
    },
    {
      path: '/oper/abnormalalarm',
      name: 'Abnormal',
      component: Abnormal
    },
    {
      path: '/oper/servicerequest',
      name: 'Service',
      component: Service
    },
    {
      path: '/oper/suggest',
      name: 'Suggest',
      component: Suggest
    },
    {
      path: '/hotel/buildings',
      name: 'Buildings',
      component: Buildings
    },
    {
      path: '/hotel/storeys',
      name: 'Storeys',
      component: Storeys
    },
    {
      path: '/hotel/roomtype',
      name: 'Roomtype',
      component: Roomtype
    },
    {
      path: '/hotel/roomsinfo',
      name: 'Roomsinfo',
      component: Roomsinfo
    },
    {
      path: '/hotel/roomarea',
      name: 'Roomarea',
      component: Roomarea
    },
    {
      path: '/hotel/device',
      name: 'Device',
      component: Device
    },
    {
      path: '/hotel/devicetype',
      name: 'Devicetype',
      component: Devicetype
    },
    {
      path: '/statis/energy',
      name: 'Energy',
      component: Energy
    },
    {
      path: '/statis/guset',
      name: 'Guest',
      component: Guest
    },
    {
      path: '/statis/chenkin',
      name: 'Checkin',
      component: Checkin
    },
    {
      path: '/account/menu',
      name: 'Themenu',
      component: Themenu
    },
    {
      path: '/account/userlist',
      name: 'Nonadmin',
      component: Nonadmin
    },
    {
      path: '/account/organiza',
      name: 'Organ',
      component: Organ
    },
    {
      path: '/account/role',
      name: 'Role',
      component: Role
    },
    {
      path: '/data/backup',
      name: 'Backup',
      component: Backup
    },
    {
      path: '/data/restore',
      name: 'Restore',
      component: Restore
    }, {
      path: '/data/timer',
      name: 'Timer',
      component: Timer
    }, {
      path: '/system/rcuapi',
      name: 'Rcuhost',
      component: Rcuhost
    }, {
      path: '/system/config',
      name: 'Theconfig',
      component: Theconfig
    }, {
      path: '/system/exception',
      name: 'Exception',
      component: Exception
    }, {
      path: '/system/servtype',
      name: 'Servtype',
      component: Servtype
    }, {
      path: '/system/hotellog',
      name: 'Hotellog',
      component: Hotellog
    }, {
      path: '/thetestss',
      name: 'Testpage',
      component: Testpage
    }, {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/syshotel/create',
      name: 'Createhotel',
      component: Createhotel
    },
    //存在未匹配的路径，代理到login
    // {
    //   path: '/*',
    //   redirect: '/login'
    // }

  ]
})



