<template>
    <div class="mainContR">
        <div class="row layer-box room-role">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">角色管理</span>
                    <span class="rightTotal">共X个角色</span>
                </div>
                <div class="user-msgs-table layerC-content-user">
                    <template>
                        <el-table
                            :data="roleList"
                            style="width: 100%"
                            class="element-table-test"
                            height="260"
                        >
                            <el-table-column type="index" label="序号" width="70"></el-table-column>
                            <el-table-column prop="roleName" label="角色名称" width="150"></el-table-column>
                            <el-table-column prop="roleCode" label="角色标识" width="130"></el-table-column>
                            <el-table-column prop="roleDesc" label="角色描述" width="150"></el-table-column>
                            <el-table-column
                                prop="createTime"
                                label="创建时间"
                                width="130"
                                :formatter="formatDate"
                            ></el-table-column>
                            <el-table-column prop="type" fixed="right" label="操作" width="150">
                                <template slot-scope="scope">
                                    <!-- <div
                                        @click="handleSearch(scope.$index, scope.row)"
                                        class="delete tableBtn el-icon-search"
                                    ></div>-->
                                    <div
                                        @click="handleUpdate(scope.$index, scope.row)"
                                        class="updata tableBtn icon-2x8"
                                    ></div>
                                    <div
                                        @click="handleOpen(scope.$index, scope.row)"
                                        class="delete tableBtn el-icon-plus"
                                    ></div>
                                    <div
                                        @click="handleDelete(scope.$index, scope.row)"
                                        class="delete tableBtn icon-2x12"
                                    ></div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
                <ul class="user-table-bottom row">
                    <li class="add-user col-xs-2 col-sm-2 col-md-2 col-lg-2">
                        <button class="add-user-btn" @click="dialogShow">添加角色</button>
                        <el-dialog
                            class="create-user-form"
                            :title="dialogtitle"
                            :visible.sync="dialogFormVisible"
                            center
                            :before-close="handleClose"
                        >
                            <ul class="form-list" v-if="dialogType == '1' ||dialogType == '2'">
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">角色名称</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            v-model="theRole.roleName"
                                            placeholder="请输入角色名称"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">角色标识</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input thenumber"
                                            placeholder="请输入角色标识"
                                            v-model="theRole.roleCode"
                                        ></el-input>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">所属组织</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            class="msg-input"
                                            placeholder="请选择所属组织"
                                            v-model="deptName"
                                            v-on="{focus:codefocus,blur:codeblur}"
                                        ></el-input>
                                        <el-dialog
                                            class="choose-organ-dialog"
                                            title="请选择"
                                            :visible.sync="dialogFormVisiblezz"
                                            center
                                            append-to-body
                                        >
                                            <div>
                                                <el-tree
                                                    :data="organizaTree"
                                                    :props="defaultProps"
                                                    highlight-current
                                                    class="organ-el-tree"
                                                    @node-click="getOrganNodeData"
                                                    :render-content="renderContent"
                                                ></el-tree>
                                            </div>
                                            <div slot="footer" class="dialog-footer">
                                                <el-button @click="dialogFormVisiblezz = false">取 消</el-button>
                                                <el-button
                                                    type="primary"
                                                    @click="organDialogConfirm"
                                                >确 定</el-button>
                                            </div>
                                        </el-dialog>
                                    </div>
                                </li>
                                <li class="form-list-li row">
                                    <p class="name col-xs-2 col-sm-2 col-md-2 col-lg-2">角色描述</p>
                                    <div class="msg col-xs-9 col-sm-9 col-md-9 col-lg-9">
                                        <el-input
                                            type="textarea"
                                            class="msg-input thenumber"
                                            placeholder="请输入角色描述"
                                            :autosize="{ minRows: 2, maxRows: 4}"
                                            v-model="theRole.roleDesc"
                                        ></el-input>
                                    </div>
                                </li>
                            </ul>
                            <div v-if="dialogType == '3'" class="form-tree">
                                <el-tree
                                    class="themenu-el-tree"
                                    :data="menuTree"
                                    :default-checked-keys="checkedKeys"
                                    node-key="id"
                                    highlight-current
                                    :props="defaultProps"
                                    show-checkbox
                                    :filter-node-method="filterNode"
                                    default-expand-all
                                ></el-tree>
                                <!-- 
                                    :default-checked-keys="rolesMenuTree"
                                    default-expand-all
                                     @node-expand="nodeExpand"
                                      @node-click="getNodeData"
                                     :render-content="renderContentMenu"
                                -->
                            </div>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="handleCancel">取 消</el-button>
                                <el-button type="primary" @click="handleConfirm">确 定</el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="talbe-paging col-xs-10 col-sm-10 col-md-10 col-lg-10">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage4"
                            :page-sizes="[100, 200, 300, 400]"
                            :page-size="100"
                            layout=" prev, pager, next, jumper, sizes"
                            :total="400"
                        ></el-pagination>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import moment from "moment";
// import request from "../../router/axios";
import axios from "axios";
export default {
    name: "Role",
    data: function() {
        return {
            defaultProps: {
                children: "children",
                label: "name"
            },
            dialogFormVisible: false,
            dialogFormVisiblezz: false, //组织的dialog
            theRole: {
                roleName: "",
                roleCode: "",
                roleDesc: "",
                roleId: ""
            },
            checkedKeys: [],
            deptId: "",
            deptName: "",
            dialogtitle: "",
            dialogType: "", //1:增加  2.编辑角色   3：权限树
            currentPage4: 1,
            statusList: [
                {
                    status: "0",
                    label: "有效"
                },
                {
                    status: "9",
                    label: "锁定"
                }
            ]
        };
    },
    computed: {
        ...mapState("dept", [
            "userList",
            "roleList",
            "menuTree",
            "organizaTree",
            "rolesMenuTree"
        ])
    },
    methods: {
        ...mapActions("dept", [
            "loadMenuTree",
            "loadUserlist",
            "loadOrganizaTree",
            "loadMenuTree",
            "loadRoleList",
            "submitNewRole",
            "submitRoleDetail",
            "deleteRole",
            "loadRolesMenuTree"
        ]),
        // menutree 预处理
        renderContent(h, { node, data, store }) {
            // console.log(node, data, store);
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        filterNode(value, data) {
            if (!value) return true;
            return data.label.indexOf(value) !== -1;
        },
        renderContentMenu: function(h, { node, data, store }) {
            console.log({ node, data, store });
            let className = "";
            if (node.childNodes.length > 0) {
                className = "chlidIcon icon--8";
            }
            if (node.expanded) {
                className = "chlidIcon icon-close-2";
            }
            return (
                <span class="custom-tree-node">
                    <i class={className} />
                    <span style="margin-left:12px;">{node.label}</span>
                </span>
            );
        },
        // tree 节点 被展开
        nodeExpand: function(data, node, row) {
            console.log("展开");
            for (let i in node.childNodes) {
                if (
                    this.rolesMenuTree.indexOf(node.childNodes[i].data.id) == -1
                ) {
                    node.childNodes[i].checked = false;
                }
            }
        },
        /**
         * 解析出所有的太监节点id
         * @param json 待解析的json串
         * @param idArr 原始节点数组
         * @param temp 临时存放节点id的数组
         * @return 太监节点id数组
         */
        resolveAllEunuchNodeId(json, idArr, temp) {
            for (let i = 0; i < json.length; i++) {
                const item = json[i];
                // 存在子节点，递归遍历;不存在子节点，将json的id添加到临时数组中
                if (item.children && item.children.length !== 0) {
                    this.resolveAllEunuchNodeId(item.children, idArr, temp);
                } else {
                    temp.push(idArr.filter(id => id === item.id));
                }
            }
            console.log(temp);
            return temp;
        },
        getNodeData: function(datas) {
            // console.log(datas);
            // this.treeForm.parentId = datas.parentId;
            // this.treeForm.deptId = datas.id;
            // this.treeForm.themenuName = datas.name;
            // this.formEdit = false;
        },
        getOrganNodeData(data) {
            console.log(data);
            this.deptId = data.id;
            this.deptName = data.name;
        },
        // handleSearch: function() {},
        // 修改角色
        handleUpdate: function(index, row) {
            this.dialogtitle = "编辑角色";
            this.dialogType = "2";
            this.theRole.roleName = row.roleName;
            this.theRole.roleCode = row.roleCode;
            this.theRole.roleDesc = row.roleDesc;
            this.theRole.roleId = row.roleId;
            this.deptId = row.deptId;
            this.deptName = row.deptName;
            this.dialogFormVisible = true;
            console.log(row);
        },
        // 权限
        handleOpen: function(index, row) {
            this.loadMenuTree();
            this.dialogtitle = "权限修改";
            this.dialogType = "3";
            let id = row.roleId;
            let access_token = sessionStorage.getItem("SESSIONACCESSTOKEN");
            axios({
                url: "/admin/menu/tree/" + id,
                headers: {
                    Authorization: "Bearer " + access_token
                },
                method: "get"
            })
                .then(res => {
                    console.log(res);
                    let datas = res.data;
                    // this.checkedKeys = res.data;
                    this.checkedKeys = this.resolveAllEunuchNodeId(
                        this.menuTree,
                        datas,
                        []
                    );
                })
                .catch(err => {
                    console.log(err);
                });
            this.dialogFormVisible = true;
        },
        // 删除角色
        handleDelete: function(index, row) {
            this.deleteRole(row.roleId);
        },
        //
        formatDate: function(date) {
            return moment(date).format("YYYY-MM-DD");
        },
        // dialog
        dialogShow: function() {
            this.dialogtitle = "添加角色";
            this.dialogType = "1";
            this.dialogFormVisible = true;
        },
        // 取消
        handleCancel: function() {
            this.dialogFormVisible = false;
            this.theRoleEmpty();
        },
        // 根据不同的类型，不同的接口
        handleConfirm: function() {
            // this.dialogFormVisible = false;
            let obj = {};
            console.log(this.dialogType);
            if (this.dialogType === "1") {
                obj.roleCode = this.theRole.roleCode;
                obj.roleDesc = this.theRole.roleDesc;
                obj.roleName = this.theRole.roleName;
                obj.deptId = this.deptId;
                console.log(obj);
                this.submitNewRole(obj);
            } else if (this.dialogType === "2") {
                obj.roleCode = this.theRole.roleCode;
                obj.roleDesc = this.theRole.roleDesc;
                obj.roleName = this.theRole.roleName;
                obj.roleId = this.theRole.roleId;
                obj.deptId = this.deptId;
                console.log(obj);
                this.submitRoleDetail(obj);
            }
            // this.theRoleEmpty();
        },
        // dialog 非点击取消和确认
        handleClose: function() {
            this.theRoleEmpty();
            this.dialogFormVisible = false;
            // this.rolesMenuTree=[]
            // console.log();
        },

        handchange: function() {},
        // 分页
        handleSizeChange: function() {},
        handleCurrentChange: function() {},
        theRoleEmpty: function() {
            this.theRole.roleName = "";
            this.theRole.roleCode = "";
            this.theRole.roleDesc = "";
            this.checkedKeys = [];
        },
        // 添加角色中的组织input 聚焦
        codefocus: function() {
            this.dialogFormVisiblezz = !this.dialogFormVisiblezz;
        },
        codeblur: function() {},
        organDialogConfirm: function() {
            this.dialogFormVisiblezz = false;
            // console.log(this.deptName);
        }
    },

    mounted() {
        this.loadOrganizaTree();

        this.loadRoleList();
    }
};
</script>
<style lang="less">
.layerC-content-role {
    min-height: 134px;
}
.room-role {
    background: #fff;
}
.user-msgs-table {
    // max-height: 300px;
    padding: 0 12px 18px;
    height: 300px;
    overflow: auto;
    .el-table {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
        // 表格头
        .el-table__header {
            th.is-leaf {
                border: 0 none;
            }
            thead {
                color: #303030;
                letter-spacing: -0.25px;
                font-weight: 500;
            }
            th {
                font-weight: 500;
            }
        }
        // 表格body
        .el-table__body {
            tbody tr {
                // border-bottom:1px solid #f00;
                &:nth-child(odd) {
                    background: #fefefe;
                }
                &:nth-child(even) {
                    background: #fafafa;
                }
            }
        }
    }
    .el-table:before {
        height: 0;
    }

    .el-table td,
    .el-table th.is-leaf {
        // border: 0 none;
    }
    // hover 时 正行的 颜色改变
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #eee !important;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
}
.user-table-bottom {
    height: 60px;
    padding: 12px 12px 24px 12px;
    margin-top: 20px;
    .add-user,
    .talbe-paging {
        height: 24px;
    }
    .add-user {
        .add-user-btn {
            height: 24px;
            font-family: PingFangSC-Semibold;
            font-size: 12px;
            color: #ffffff;
            background: #86b3e7;
            letter-spacing: -0.23px;
            text-align: center;
            padding: 0 12px;
            line-height: 24px;
            border-radius: 6px;
        }
    }
}
.create-user-form {
    .el-dialog {
        // dialog 标题
        .el-dialog__header {
            .el-dialog__title {
                font-family: PingFangSC-Semibold;
                font-size: 14px;
                color: #303030;
                letter-spacing: -0.25px;
                text-align: center;
                font-weight: bolder;
            }
        }
    }
    .form-list {
        .form-list-li {
            margin-bottom: 20px;
            height: 28px;
            .name,
            .msg {
                float: left;
            }
            .name {
                // width: 92px;
                height: 28px;
                text-align: right;
                line-height: 28px;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #303030;
                letter-spacing: -0.25px;
            }
            .msg {
                padding-left: 16px;
                height: 28px;
                // width: 80%;
                .el-autocomplete {
                    width: 100%;
                }
                .el-select {
                    width: 100%;
                }
                .msg-input {
                    //  width: 100%;
                    .el-input__inner {
                        height: 28px;
                        border: 0 none;
                        background: #f4f4f4;
                        border-radius: 6px;
                    }

                    .el-input__suffix {
                        height: 28px;

                        .el-input__icon {
                            line-height: 28px;
                        }
                    }
                }
            }
        }
    }
    .form-tree {
        min-height: 300px;
    }
}
.choose-organ-dialog {
}
</style>


