<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <!-- 替换为搜索内容 -->
                <ul class="select-content">
                    <li class="select-li authorVal">
                        <el-input v-model="searchVal" placeholder="请输入搜索内容"></el-input>
                    </li>
                    <li class="select-li authorBTN">
                        <span class="select-li-confirm mg__confirm__btn" @click="abnormalSearch">搜索</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="row layer-box room-author">
            <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">客控授权管理</span>
                    <span class="rightTotal">共{{theTableLength}}个授权</span>
                </div>
                <div class="layerC-content-author">
                    <div class="auth-msgs-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                            >
                                <el-table-column type="index" label="序号" width="70"></el-table-column>
                                <el-table-column prop="room" label="房号" width="120"></el-table-column>
                                <el-table-column prop="type" label="手机号码" width="180"></el-table-column>
                                <el-table-column prop="time" label="房间类型"></el-table-column>
                                <el-table-column prop="content" label="授权的开始时间"></el-table-column>
                                <el-table-column prop="solve" label="授权的结束时间"></el-table-column>
                            </el-table>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import moment from "moment";
export default {
    name: "Authorization",
    data: function() {
        return {
            tableData: [
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                },
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                },
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                },
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                },
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                }
            ],
            searchVal: ""
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },
    methods: {
        // 头部搜索
        abnormalSearch: function() {
            console.log("search");
        }
    }
};
</script>
<style lang="less" >
.layerC-content-author {
    min-height: 134px;
    max-height: 570px;
    overflow: auto;
}
.room-author {
    background: #fff;
}
.authorVal {
    width: 86%;
    padding: 0 12px 0 10px;
}
.authorBTN {
    width: 12%;
    padding: 0 12px 0 10px;
    .select-li-confirm {
        display: block;
        height: 28px;
        line-height: 28px;
    }
}
.auth-msgs-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }

    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
}
</style>


