<template>
    <div class="mainContR">
        <div class="rpage-header row layer-box">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <!-- 替换为搜索内容 -->
                <ul class="select-content">
                    <li class="select-li authorVal">
                        <el-input v-model="searchVal" placeholder="请输入搜索内容"></el-input>
                    </li>
                    <li class="select-li authorBTN">
                        <span class="select-li-confirm mg__confirm__btn" @click="abnormalSearch">搜索</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="row layer-box room-build">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">楼宇信息管理</span>
                    <span class="rightTotal">共{{theTableLength}}个楼宇信息</span>
                </div>
                <div class="layerC-content-build">
                    <!-- 楼宇信息列表 -->
                    <div class="auth-msgs-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                            >
                                <el-table-column type="index" label="序号"></el-table-column>
                                <el-table-column prop="room" label="楼宇编号"></el-table-column>
                                <el-table-column prop="type" label="楼宇名称"></el-table-column>

                                <el-table-column prop="asd" fixed="right" label="操作" width="150">
                                    <template slot-scope="scope">
                                        <div
                                            @click="handleUpdate(scope.$index, scope.row)"
                                            class="updata tableBtn icon-2x8"
                                        ></div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Buildings",
    data: function() {
        return {
            tableData: [
                {
                    room: 101,
                    type: "请求",
                    time: "9:00",
                    content: "水",
                    solve: "yes"
                }
            ],
            searchVal: ""
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },
    methods: {
        abnormalSearch: function() {
            console.log("搜索");
        },
        // 楼宇
        handleUpdate: function() {
            console.log("异常修改");
        }
    }
};
</script>
<style lang="less">
.layerC-content-build {
    min-height: 134px;
}
.room-build {
    background: #fff;
}
.authorVal {
    width: 86%;
    padding: 0 12px 0 10px;
}
.authorBTN {
    width: 12%;
    padding: 0 12px 0 10px;
    .select-li-confirm {
        display: block;
        height: 28px;
        line-height: 28px;
    }
}
.auth-msgs-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
}
</style>


