<template>
    <div class="loginIndex">
        <div class="login-cont row">
            <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6 login-cont-left">
                <div class="form-logo">
                    <!-- u:{{username}}
                    p:{{password}}-->
                    <!-- str:{{thecode.teststr}}
                    c:{{code}} -->
                </div>
                <div class="form-account form-lists">
                    <span class="list-icon icon-l-user" :class="{iconColor30:isuserinput}"></span>
                    <input
                        type="text"
                        id="login-username"
                        placeholder="请输入用户名"
                        v-model="username"
                        v-on="{focus:userfocus,blur:userblur}"
                    >
                </div>
                <div class="form-password form-lists">
                    <span class="list-icon icon-l-psw" :class="{iconColor30:ispwsinput}"></span>
                    <input
                        type="password"
                        id="login-pwd"
                        placeholder="请输入密码"
                        v-model="password"
                        v-on="{focus:pwdfocus,blur:pwdblur}"
                    >
                </div>
                <div class="form-code form-lists">
                    <span class="list-icon icon-l-psw" :class="{iconColor30:iscodeinput}"></span>
                    <input
                        type="text"
                        id="login-code"
                        placeholder="请输入验证码"
                        v-model="code"
                        v-on="{focus:codefocus,blur:codeblur}"
                    >
                    <img :src="thecode.src" @click="refreshCode" class="theimg">
                </div>
                <div class="form-login-btn form-lists">
                    <el-button type="primary" class="login-btn" @click="handleLogin">
                        <!-- <router-link to="/theHomePage">登陆</router-link> -->
                        登陆
                    </el-button>
                </div>
            </div>
            <div class="hidden-xs hidden-sm col-md-6 col-lg-6 login-cont-right"></div>
        </div>
    </div>
</template>

<script>
// iconColor30
// import CryptoJS from "crypto-js/crypto-js";
import { encryption, randomLenNum } from "@/devtools/devtool";
import HTTPED from "../../httpdata/httpdata";
import axios from "axios";
export default {
    name: "Login",
    data: function() {
        return {
            isuserinput: false,
            ispwsinput: false,
            iscodeinput: false,
            username: "",
            password: "",
            code: "",
            thecode: {
                src: "/code",
                len: 4,
                teststr: ""
            },
            logindata: {
                // code: "asdf",
                password: "123456",
                // randomStr: "18341558917550744",
                // username: "xxjd_admin"
                username: "admin"
            }
        };
    },
    created() {
        this.refreshCode();
    },
    methods: {
        refreshCode() {
            // let theurl = `http://192.168.0.140:9999/code`;
            let theurl = `/code`;
            // console.log(theurl);
            let aaa = randomLenNum(this.thecode.len, true);
            this.thecode.src = `${theurl}?randomStr=${aaa}`;
            // console.log(this.thecode.src.split("=")[1]);
            this.thecode.teststr = this.thecode.src.split("=")[1];
        },
        userfocus() {
            this.isuserinput = !this.isuserinput;
        },
        userblur() {
            this.isuserinput = !this.isuserinput;
        },
        pwdfocus() {
            this.ispwsinput = !this.ispwsinput;
        },
        pwdblur() {
            this.ispwsinput = !this.ispwsinput;
        },
        codefocus() {
            this.iscodeinput = !this.iscodeinput;
        },
        codeblur() {
            this.iscodeinput = !this.iscodeinput;
        },

        handlerencrypt() {
            let userinfo = encryption({
                data: this.logindata,
                key: "thanks,pig4cloud",
                param: ["password"]
            });
            console.log(userinfo.password);
        },
        handleLogin() {
            let userinfo = encryption({
                data: this.logindata,
                key: "thanks,pig4cloud",
                param: ["password"]
            });
            let scope = "server";
            let grant_type = "password";
            let username = this.logindata.username,
                password = userinfo.password,
                randomStr = this.thecode.teststr,
                code = this.code;
            axios({
                url: "/auth/oauth/token",
                headers: {
                    isToken: false,
                    Authorization: "Basic cGlnOnBpZw=="
                },
                method: "post",
                params: {
                    username,
                    password,
                    randomStr,
                    code,
                    grant_type,
                    scope
                }
            })
                .then(res => {
                    console.log(res.data);
                    sessionStorage.setItem(
                        "SESSIONACCESSTOKEN",
                        res.data.access_token
                    );
                    sessionStorage.setItem(
                        "SESSIONTOKENTYPE",
                        res.data.token_type
                    );
                    let access_token = res.data.access_token;

                    // console.log(sessionStorage.getItem("SESSIONACCESSTOKEN"));

                    axios({
                        url: "/admin/hotel/getHotelToken",
                        headers: {
                            Authorization: "Bearer " + access_token
                        },
                        method: "get"
                    })
                        .then(res => {
                            console.log(res.data.data);
                            sessionStorage.setItem(
                                "SESSIONACCESSHOTELTOKEN",
                                res.data.data
                            );
                            this.$router.push({ path: "/theHomePage" });
                        })
                        .catch(err => {
                            console.log(err);
                        });
                })
                .catch(err => {
                    console.log(err);
                });
        }
    },
    computed: {}
};
</script>
<style lang="less" scoped>
.loginIndex {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: auto;
    .login-cont {
        min-width: 836px;
        height: 456px;
        .login-cont-left {
            background: #fff;
            height: 100%;
            padding: 0 58px;
            .form-logo {
                height: 129px;
            }
            .form-account,
            .form-password {
                border-bottom: 1px solid #ccc;
            }
            .form-lists {
                height: 56px;
                .list-icon {
                    height: 56px;
                    width: 24px;
                    float: left;
                    line-height: 56px;
                    text-align: center;
                    font-size: 30px;
                    color: #303030;
                }
                #login-username,
                #login-pwd,
                #login-code {
                    float: left;
                    height: 55px;
                    line-height: 55px;
                    padding-left: 16px;
                    font-family: PingFangSC-Semibold;
                    font-size: 14px;
                    color: #303030;
                    letter-spacing: -0.34px;
                }
                #login-username,
                #login-pwd {
                    width: 75%;
                }
            }
            .form-login-btn {
                margin-top: 16px;
                .login-btn {
                    width: 100%;
                    background: #86b3e7;
                    border-radius: 8px;
                    border: 0 none;
                    height: 48px;
                    font-family: PingFangSC-Semibold;
                    font-size: 14px;
                    color: #ffffff;
                    letter-spacing: 0.17px;
                    text-align: center;
                    padding: 0;
                    a {
                        display: block;
                        widows: 100%;
                        height: 100%;
                        line-height: 48px;
                        color: #fff;
                    }
                }
            }
        }
        .login-cont-right {
            background: url(../../assets/imgs/loginR.png) center center
                no-repeat;
            height: 100%;
        }
    }
}
</style>



