import axios from "axios";
import request from '../../router/axios';
const state = {
  allRoomList: {},
  roomStatusCount: {},
  devicesList: {}, //设备列表 
  devicesListSwitch: {}, //普通灯开关列表
  // alldevices: { pt: false },
  devicesListTG: {},
  devicesTypeCL: [], //窗帘的数组
  devicesListCL: {}, // 窗帘数组的数据
  devicesTypeWK: [], //空调的数组
  devicesListWK: {}, //空调数组的数据
  devicesTypeBGM: [], // bgm 的数组
  devicesListBGM: {}, //bgm数组的数据
  dataMsg: false,
  alldevices: {
    pt: false
  },
  // 酒店列表
  hotelList: []
};
const getters = {
  getAllRoomStatus(state) {
    return state.roomStatusCount
  }
};
const mutations = {
  // 房间列表
  getRoomlist(state, roomstatus) {
    request({
        url: "/data/room/getAllRoomsByFloor",
        method: "get",
        params: {
          roomStatus: roomstatus
        }
      })
      .then(res => {
        console.log(res);
        state.allRoomList = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  getRoomStatusCounts(state) {
    request({
        url: "/data/room/getRoomStatusCount",
        method: "get"
      })
      .then(res => {
        // console.log(res.data.data);
        state.roomStatusCount = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 通过RCU 获得的单个房间的设备列表并做处理
  get_roomDevicesBYrcutoken(state) {
    request({
      url: "/data/device/getDevicesByRcuToken",
      method: "get",
      params: {
        rcuToken: "02171312ffff0202"
        // rcuToken: this.rcuToken
      }
    }).then(res => {
      function hexString2Btye(str) {
        let pos = 0;
        let len = str.length;
        if (len % 2 != 0) {
          return null;
        }
        len /= 2;
        let hexA = new Array();
        for (let i = 0; i < len; i++) {
          let s = str.substr(pos, 2);
          let v = parseInt(s, 16);
          hexA.push(v);
          pos += 2;
        }

        // console.log(hexA);
        return hexA;
      }
      // console.log(res);
      // this.devicesList ;
      let thedata = res.data.data;

      let obj = {},
        switch1value = {};
      for (let j = 0, len = thedata.length; j < len; j++) {
        let items = thedata[j];
        // 创建一个对象，key为 deviceType，value为同类deviceType 组成的数组
        if (items.deviceType in obj) {
          obj[items.deviceType].push(items);
        } else {
          obj[items.deviceType] = [];
          obj[items.deviceType].push(items);
        }
      }
      // console.log(obj);
      // bgm
      state.devicesTypeBGM = obj[44];
      let objBGM = {}
      for (let name in obj[44]) {
        let items = obj[44][name];
        let deviceId_i = items.deviceId;
        let deviceId_i_status = items.deviceId + 'status';
        let deviceId_i_vol = items.deviceId + 'vol';        
        let deviceId_i_mute = items.deviceId + 'mute';
        let deviceId_i_model = items.deviceId + 'model';
        console.log(items);

        let arr = hexString2Btye(items.statusData);
        console.log(arr);
        objBGM[deviceId_i_status] = items.status;
        objBGM[deviceId_i_vol] = arr[0];
        // objBGM[deviceId_i_play] = arr[0];
        objBGM[deviceId_i_mute] = arr[4];
        objBGM[deviceId_i_model] = arr[5];
      }
      state.devicesListBGM = objBGM;
      console.log(obj[44]);
      //普通灯 值 对象
      let objpt = {};
      for (let name in obj[1]) {
        let items = obj[1][name];
        let deviceId_i = items.deviceId;
        if (items.status == 1) {
          objpt[deviceId_i] = true;
        } else {
          objpt[deviceId_i] = false;
        }
      }
      // console.log(objpt);
      state.devicesListSwitch = objpt;
      //调光灯 值 对象
      let objtg = {};
      for (let name in obj[2]) {
        let items = obj[2][name];
        let deviceId_i = items.deviceId;
        if (items.status == 1) {
          objtg[deviceId_i] = parseInt(items.statusData, 16);
        } else {
          objtg[deviceId_i] = 0;
        }
      }
      // console.log(objtg);
      state.devicesListTG = objtg;
      // 温控器
      state.devicesTypeWK = obj[87];
      let objwk = {};
      for (let name in obj[87]) {
        let items = obj[87][name];
        let deviceId_i = items.deviceId;
        let deviceId_i_status = items.deviceId + 'status';
        let deviceId_i_mode = items.deviceId + 'mode';
        let deviceId_i_temp = items.deviceId + 'temp';
        let deviceId_i_speed = items.deviceId + 'speed';

        let stateValues = hexString2Btye(items.statusData);
        // console.log(stateValues);
        let version = items.versionNo;
        // console.log(version);
        if (version >= 0x45) {
          if (stateValues.length >= 6) {
            let modeByte = stateValues[4];
            let temp = stateValues[1] / 2;
            let speed = stateValues[5];
          }
        } else {

          let modeByte = (stateValues[0] >> 5) & 0x7;
          let speed = (stateValues[0] >> 3) & 0x3;
          let temp;
          // console.log();
          if (version < 0x24) {
            temp = stateValues[1];
          } else {
            temp = stateValues[1] / 2;
          }
          // console.log(modeByte, speed, temp);
          objwk[deviceId_i_status] = items.status;
          objwk[deviceId_i_mode] = modeByte;
          objwk[deviceId_i_temp] = temp;
          objwk[deviceId_i_speed] = speed;
        }
      }

      // console.log(objwk);
      state.devicesListWK = objwk;

      // 所有窗帘
      state.devicesTypeCL = obj[17].concat(obj[29]);
      let all = state.devicesTypeCL;
      // console.log(all);
      let objcl = {};
      for (let name in all) {
        let items = all[name];
        let deviceId_i = items.deviceId;
        let deviceId_i_type = deviceId_i + "type";
        let deviceId_i_status = deviceId_i + "status";
        let deviceId_i_xc = deviceId_i + "xc";
        let deviceId_i_jd = deviceId_i + "jd";
        objcl[deviceId_i_type] = {};

        // objcl = { decicesID  设备id: { status  开关状态: 0, xc 行程: 00, jd 角度: 00 } };
        let bytearr = hexString2Btye(items.statusData);
        objcl[deviceId_i_xc] = bytearr[0] == undefined ? 0 : bytearr[0];
        objcl[deviceId_i_jd] = bytearr[1] == undefined ? 0 : bytearr[1];
        if (items.status == 16 || items.status == 19) {
          objcl[deviceId_i_status] = 0;
        } else if (items.status == 17 || items.status == 20) {
          objcl[deviceId_i_status] = 1;
        } else {
          objcl[deviceId_i_status] = 2;
        }
        if (
          items.deviceType == 17 ||
          items.deviceType == 18 ||
          items.deviceType == 23
        ) {
          objcl[deviceId_i_type] = 1;
        } else if (
          items.deviceType == 28 ||
          items.deviceType == 19 ||
          items.deviceType == 29
        ) {
          objcl[deviceId_i_type] = 3;
        } else {
          objcl[deviceId_i_type] = 2;
        }
      }
      // console.log(objcl);

      state.devicesListCL = objcl;
      state.devicesList = obj;

    }).catch(err => {
      console.log(err);
    })

  },
  // 单个设备调控
  get_controlDeviceByStr(state, deviceObj) {
    let obj = JSON.parse(deviceObj);
    console.log(obj);
    // 温度 加减控制
    if (obj.command == "aircon-temp-add") {
      let idstemp = obj.id + 'temp';
      let thetemp = state.devicesListWK[idstemp];
      let temp2 = obj.commandData;
      if (temp2 == 'add') {
        thetemp = thetemp + 1;
      } else {
        thetemp = thetemp - 1;
      }
      if (thetemp < 16) {
        thetemp = 16;
        state.devicesListWK[idstemp] = 16;
      } else if (thetemp > 32) {
        thetemp = 32;
        state.devicesListWK[idstemp] = 32;
      } else {
        let data = thetemp.toString(16);
        obj.commandData = data.toString(16);
        // console.log(obj);
        let str = JSON.stringify(obj)
        request({
            url: "/data/device/controlDeviceByStr",
            method: "get",
            params: {
              rcuToken: "02171312ffff0202",
              jsonCommand: str
            }
          }).then(res => {
            // console.log(res.data);
          })
          .catch(err => {
            // console.log(err);
          })
      }
    } else {
      // 非温度加减
      request({
          url: "/data/device/controlDeviceByStr",
          method: "get",
          params: {
            rcuToken: "02171312ffff0202",
            jsonCommand: deviceObj
          }
        }).then(res => {
          // console.log(res.data);
        })
        .catch(err => {
          // console.log(err);
        })
    }

  },
  // 设备列表中的总控
  get_controlDevicesByType(state, obj) {
    // console.log(obj);
    let command = obj.command ? "light-on" : "light-off";
    request({
        url: "/data/device/controlDevicesByType",
        method: "get",
        params: {
          rcuToken: "02171312ffff0202",
          deviceType: obj.type,
          command: command
        }
      }).then(res => {
        if (obj.type == 1) {
          state.alldevices.pt = obj.command;
        }
      })
      .catch(err => {
        state.alldevices.pt = true;
        console.log(err);
      })
  },
  // rcu 返回的数据
  rcu_message(state, obj) {
    // console.log(obj);
    function hexString2Btye(str) {

      let pos = 0;
      let len = str.length;
      if (len % 2 != 0) {
        return null;
      }
      len /= 2;
      let hexA = new Array();
      for (let i = 0; i < len; i++) {
        let s = str.substr(pos, 2);
        let v = parseInt(s, 16);
        hexA.push(v);
        pos += 2;
      }

      // console.log(hexA);
      return hexA;
    }

    for (let j = 0, len = obj.length; j < len; j++) {
      let item = obj[j];
      // console.log(item);
      if (item.deviceType == 1) {
        // console.log(state.devicesListSwitch);
        if (item.status == 1) {
          state.devicesListSwitch[item.deviceId] = true;
        } else {
          state.devicesListSwitch[item.deviceId] = false;
        }

      } else if (item.deviceType == 2) {
        // console.log(state.devicesListTG);
        if (item.status == 0) {
          state.devicesListTG[item.deviceId] = 0;
        } else {
          let nums = parseInt(item.statusData, 16);
          state.devicesListTG[item.deviceId] = nums;
          // console.log(nums);
          // state.devicesListTG[item.deviceId] = false;
        }
      } else if (item.deviceType == 29) {
        // console.log(state.devicesListCL);
        let arrs = hexString2Btye(item.statusData)
        state.devicesListCL[item.deviceId + 'xc'] = arrs[0];
        state.devicesListCL[item.deviceId + 'jd'] = arrs[1];
        if (item.status == 16 || item.status == 19) {
          state.devicesListCL[item.deviceId + 'status'] = 0;
        } else if (item.status == 17 || item.status == 20) {
          state.devicesListCL[item.deviceId + 'status'] = 1;
        } else {
          state.devicesListCL[item.deviceId + 'status'] = 2;
        }
        if (
          item.deviceType == 17 ||
          item.deviceType == 18 ||
          item.deviceType == 23
        ) {
          state.devicesListCL[item.deviceId + 'type'] = 1;
        } else if (
          item.deviceType == 28 ||
          item.deviceType == 19 ||
          item.deviceType == 29
        ) {
          state.devicesListCL[item.deviceId + 'type'] = 3;
        } else {
          state.devicesListCL[item.deviceId + 'type'] = 2;
        }
      } else if (item.deviceType == 87) {
        let stateValues = hexString2Btye(item.statusData);
        // console.log(stateValues);
        let version = item.versionNo;
        let deviceId_i = item.deviceId;
        let deviceId_i_status = item.deviceId + 'status';
        let deviceId_i_mode = item.deviceId + 'mode';
        let deviceId_i_temp = item.deviceId + 'temp';
        let deviceId_i_speed = item.deviceId + 'speed';
        if (version >= 0x45) {
          if (stateValues.length >= 6) {
            let modeByte = stateValues[4];
            let temp = stateValues[1] / 2;
            let speed = stateValues[5];
          }
        } else {
          let modeByte = (stateValues[0] >> 5) & 0x7;
          let speed = (stateValues[0] >> 3) & 0x3;
          let temp;
          // console.log(version);
          if (version < 0x24) {
            temp = stateValues[1];
          } else {
            temp = stateValues[1] / 2;
          }
          // console.log(modeByte, speed, temp);
          state.devicesListWK[deviceId_i_status] = item.status;
          state.devicesListWK[deviceId_i_mode] = modeByte;
          state.devicesListWK[deviceId_i_temp] = temp;
          state.devicesListWK[deviceId_i_speed] = speed;
        }
      }else if (item.deviceType ==44) {
        let stateValues = hexString2Btye(item.statusData);
        console.log(stateValues);
        let deviceId_i = item.deviceId;
        let deviceId_i_status = item.deviceId + 'status';
        let deviceId_i_vol = item.deviceId + 'vol';        
        let deviceId_i_mute = item.deviceId + 'mute';
        let deviceId_i_model = item.deviceId + 'model';
        
        state.devicesListBGM[deviceId_i_status] = item.status;
        state.devicesListBGM[deviceId_i_vol] = stateValues[0];
        state.devicesListBGM[deviceId_i_mute] = stateValues[4];
        state.devicesListBGM[deviceId_i_model] = stateValues[5];
        
      }
    }
  },
  // 获得酒店列表
  getSysHotelPage(state) {
    request({
        url: "/admin/hotel/page",
        method: "get",
      }).then(res => {
        // console.log(res)
        state.hotelList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      })
  },
  postNewHotelInDept(state, obj) {
    request({
        url: "/admin/hotel",
        method: "POST",
        data: obj
      }).then(res => {
        console.log(res)
        // state.hotelList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      })
  },
  // 删除酒店
  deleteHotel(state, id) {
    request({
        url: "/admin/hotel/" + id,
        method: "delete",
      }).then(res => {
        console.log(res)
        // state.hotelList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      })
  },
  // 修改酒店详情
  putHotelDetail(state, obj) {
    request({
        url: "/admin/hotel",
        method: "put",
        data: obj
      }).then(res => {
        console.log(res)
        // state.hotelList = res.data.data.records;
      })
      .catch(err => {
        console.log(err);
      })
  }
};
const actions = {
  loadRoomLists(context, roomstatus) {
    context.commit('getRoomlist', roomstatus)
  },
  loadRoomStatusCount(context) {
    context.commit('getRoomStatusCounts')
  },
  // 单个设备调控
  oneDeviceChange(context, obj) {
    context.commit('get_controlDeviceByStr', obj)
  },
  // 设备列表中的总控
  allDevicesChange(context, obj) {
    context.commit('get_controlDevicesByType', obj)
  },
  // 获得单个 rcu (房间)中的设备
  getroomDevicesByRCU(context, obj) {
    context.commit('get_roomDevicesBYrcutoken')
  },
  // 接受rcu
  rcu_messageArrived(context, obj) {
    let rcu_messageType = JSON.parse(obj)
    if (rcu_messageType.MessageType == 100) {
      context.commit('rcu_message', rcu_messageType.ids)
    }
  },
  // 酒店列表
  loadSysHotelList(context) {
    context.commit('getSysHotelPage')
  },
  // 新建酒店
  submitCreateHotel(context, obj) {
    context.commit('postNewHotelInDept', obj)
  },
  // 删除酒店
  submitDeleteHotel(context, id) {
    context.commit('deleteHotel', id)
  },
  // 修改酒店详情
  submitHotelDetail(context, obj) {
    context.commit('putHotelDetail', obj)
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



