import {
  loginByUsername
} from '@/api/login';

const state = {
  testNum: 123
};
const getters = {
  getTestNum() {
    return state.testNum
  }
};
const mutations = {
  addTestNum(state, nums) {
    state.testNum += nums
  },
  subTestNum(state, nums) {
    state.testNum -= nums
  }
};
const actions = {
  getNewTestNum(context, nums) {
    context.commit('addTestNum', nums)
  },
  getNewTestNumsub(context, nums) {
    context.commit('subTestNum', nums)
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}



