<template>
    <div class="mainContR">
        <div class="row layer-box room-host">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">主机管理</span>
                    <span class="rightTotal">共X个主机</span>
                </div>
                <div class="layerC-content-host">
                    <!-- 已存在定时器列表 -->
                    <div class="suggest-table">
                        <template>
                            <el-table
                                :data="tableData"
                                style="width: 100%"
                                class="element-table-test"
                                :height="theTableHeight"
                                @selection-change="selectionChange"
                            >
                                <el-table-column type="selection" width="55"></el-table-column>
                                <el-table-column type="index" label="序号"></el-table-column>
                                <el-table-column prop="hotel" label="智能主机IP地址"></el-table-column>
                                <el-table-column prop="room" label="状态"></el-table-column>
                                <el-table-column prop="reqname" label="房号"></el-table-column>

                                <el-table-column prop="asd" fixed="right" label="操作" width="150">
                                    <template slot-scope="scope">
                                        <div
                                            @click="handleUpdate(scope.$index, scope.row)"
                                            class="updata tableBtn icon-2x8"
                                        ></div>
                                        <div
                                            @click="handleDelete(scope.$index, scope.row)"
                                            class="delete tableBtn icon-2x12"
                                        ></div>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                    </div>
                </div>
                <div class="layerC-bottom-host">
                    <div class="create-btn mg__confirm__btn" @click="handleCreate">新建主机</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Rcuhost",
    data: function() {
        return {
            tableData: [
                {
                    room: 101,
                    hotel: "如家",
                    reqname: "aaaa",
                    phone: "123456789",
                    resname: "admin",
                    tousuTime: "2018-08-16 19:32:58",
                    huifuTime: "2018-08-19 11:22:33",
                    type: "投诉"
                }
            ],
            searchVal: "",
            value: "",
            options: [
                {
                    value: "0",
                    label: "全部"
                },
                {
                    value: "1",
                    label: "设备自身故障"
                },
                {
                    value: "2",
                    label: "预制逻辑异常"
                }
            ]
        };
    },
    computed: {
        theTableHeight: function() {
            return this.tableData.length * 49 + 47;
        },
        theTableLength: function() {
            return this.tableData.length;
        }
    },
    methods: {
        // 搜索按钮
        abnormalSearch: function() {
            console.log("search");
        },
        //表格全选时
        selectionChange: function(row) {
            // row 选择项发生变化时
            console.log(row);
        },
        // 处理异常
        handleUpdate: function() {
            console.log("修改");
        },
        handleDelete: function() {
            console.log("删除");
        },
        // 新建主机
        handleCreate:function() {
            console.log('新建主机');
        }
    }
};
</script>
<style lang="less">
.layerC-content-host {
    min-height: 134px;
}
.room-host {
    background: #fff;
}
.layerC-bottom-host {
    height: 78px;
    padding: 24px;
    .create-btn {
        float: right;
        height: 24px;
        line-height: 24px;
        padding: 0 12px;
    }
}

.suggest-table {
    padding: 0 12px 30px;
    .el-table .cell {
        font-family: PingFangSC-Regular;
        font-size: 11px;
        color: #303030;
        letter-spacing: -0.23px;
    }

    .el-table--enable-row-hover .el-table__body tr:hover {
        background: #f4f4f4 !important;
        border-radius: 8px !important;
    }
    .el-table td,
    .el-table th.is-leaf {
        border: 0 none;
    }
    // 操作按钮
    .tableBtn {
        float: left;
        width: 24px;
        height: 24px;
        font-size: 24px;
        margin-right: 8px;
        cursor: pointer;
    }
    //
    .tableTag {
        margin-bottom: 2px;
    }
    .element-table-test {
        // checked box 修改颜色
        .el-checkbox__input.is-checked .el-checkbox__inner,
        .el-checkbox__input.is-indeterminate .el-checkbox__inner {
            background-color: #86b3e7;
            border-color: #86b3e7;
        }
        .el-checkbox__inner:hover {
            border-color: #86b3e7;
        }
        .el-checkbox {
            .el-checkbox__inner {
                border-radius: 50%;
            }
        }
    }
}
</style>


