                                    :value="item.value"
                                ></el-option>
                            </el-select>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <!-- 下方内容循环加载为几号楼 -->
        <div class="row layer-box room-jks">
            <div class="col-sm-12 col-md-12 col-lg-12">
                <div class="layerC-header">
                    <span class="leftTitle">X号楼</span>
                    <span class="rightTotal">共{{ sernums }}个房间</span>
                </div>
            </div>
            <div class="layerC-content-homepage col-sm-12 col-md-12 col-lg-12">
                <!-- 循环加载房间列表 -->
                <!-- <dl class="floor-rooms-index"> -->
                <dl class="floor-rooms-index" v-for="(value,name,index) in roomList" :key="index">
                    <dt class="rooms-index-tit">{{name}}层</dt>
                    <dd class="rooms-index-lists">
                        <!-- <ul class="lists-cont clearfix">
                            <li
                                class="lists-theroom"
                                :class="('lists-theroom-'+value2.roomStatus)"
                                :data-cls="value2.roomStatus"
                                v-for="(value2,name2,index2) in value"
                                :key="index2"
                            >
                                <div class="lists-theroom-type"></div>
                                <div class="lists-theroom-info">
                                    <div class="info-num">{{value2.roomId}}</div>
                                </div>
                            </li>
                        </ul>-->
                        <div class="lists-cont clearfix">
                            <router-link
                                v-for="(value2,name2,index2) in value"
                                :key="index2"
                                :to="{name: 'Roomdetail', params: {id: value2.roomId,rcuToken:value2.rcuToken},query:{}}"
                            >
                                <div
                                    class="lists-theroom"
                                    :class="('lists-theroom-'+value2.roomStatus)"
                                    :data-cls="value2.roomStatus"
                                    @click="handleroomdetail(value2.roomId)"
                                >
                                    <div class="lists-theroom-type"></div>
                                    <div class="lists-theroom-info">
                                        <div class="info-num">{{value2.roomId}}</div>
                                    </div>
                                </div>
