</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import axios from "axios";
import echarts from "echarts";
export default {
    name: "RoomIndex",
    components: {},
    data: function() {
        return {
            sernums: 6,
            options: [
                {
                    value: "1",
                    label: "1号楼"
                },
                {
                    value: "2",
                    label: "2号楼"
                },
                {
                    value: "3",
                    label: "3号楼"
                },
                {
                    value: "4",
                    label: "4号楼"
                },
                {
                    value: "5",
                    label: "5号楼"
                }
            ],
            value: ""
        };
    },
    computed: {
        ...mapState({
            roomList: state => state.room.allRoomList,
            roomStatus: state => state.room.roomStatusCount
        })
    },
    methods: {
        ...mapActions("room", ["loadRoomLists"]),
        loadEcharts: function(obj) {
            let this_ = this;
            !obj[100] ? (obj[100] = { name: "空净房", num: 0 }) : (obj = obj);
            !obj[101]
                ? (obj[101] = { name: "空净故障房", num: 0 })
                : (obj = obj);
            !obj[300] ? (obj[300] = { name: "入住房", num: 0 }) : (obj = obj);
            !obj[301]
                ? (obj[301] = { name: "入住故障房", num: 0 })
                : (obj = obj);
            !obj[302]
                ? (obj[302] = { name: "打扫入住房", num: 2 })
                : (obj = obj);
            !obj[303]
                ? (obj[303] = { name: "打扫入住故障房", num: 4 })
                : (obj = obj);
            !obj[400] ? (obj[400] = { name: "空脏房", num: 0 }) : (obj = obj);
            !obj[401]
                ? (obj[401] = { name: "空脏故障房", num: 0 })
                : (obj = obj);
            !obj[500] ? (obj[500] = { name: "在打扫", num: 0 }) : (obj = obj);
            !obj[501]
                ? (obj[501] = { name: "在打扫故障房", num: 0 })
                : (obj = obj);
            let allCount =
                obj[100].num +
                obj[101].num +
                obj[300].num +
                obj[301].num +
                obj[302].num +
                obj[303].num +
                obj[400].num +
                obj[401].num +
                obj[500].num +
                obj[501].num;
            function searchRoOmList(status) {
                this_.loadRoomLists(status);
            }

            let myChart1 = echarts.init(
                    document.getElementsByClassName("list-kj")[0]
                ),
                myChart2 = echarts.init(
                    document.getElementsByClassName("list-rz")[0]
                ),
                myChart3 = echarts.init(
                    document.getElementsByClassName("list-rzqj")[0]
                ),
                myChart4 = echarts.init(
                    document.getElementsByClassName("list-zl")[0]
                ),
                myChart5 = echarts.init(
                    document.getElementsByClassName("list-tfqj")[0]
                ),
                myChart6 = echarts.init(
                    document.getElementsByClassName("list-all")[0]
                );
            let option1 = {
                title: {
                    text: obj[100].num + obj[101].num,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        hoverAnimation: false, //起始位置
                        // silent: true,
                        avoidLabelOverlap: false,
                        startAngle: 270, //其实角度 0的位置是在3点钟 逆时针计算
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#86B3E7", "#FFE194"],

                        data: [
                            {
                                value: obj[100].num,
                                name: obj[100].name,
                                id: 100
                            },
                            {
                                value: obj[101].num,
                                name: obj[101].name,
                                id: 101
                            }
                        ]
                    }
                ]
            };
            let option2 = {
                title: {
                    text: obj[300].num + obj[300].num,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        hoverAnimation: false,
                        // silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#C1E080", "#F58091"],
                        data: [
                            {
                                value: obj[300].num,
                                name: obj[300].name,
                                id: 300
                            },
                            {
                                value: obj[301].num,
                                name: obj[301].name,
                                id: 301
                            }
                        ]
                    }
                ]
            };
            let option3 = {
                title: {
                    text: obj[302].num + obj[303].num,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        hoverAnimation: false,
                        // silent: true,
                        minAngle: 15,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#FAD650", "#DC7FE2"],

                        data: [
                            {
                                value: obj[302].num,
                                name: obj[302].name,
                                id: 302
                            },
                            {
                                value: obj[303].num,
                                name: obj[303].name,
                                id: 303
                            }
                        ]
                    }
                ]
            };
            let option4 = {
                title: {
                    text: obj[400].num + obj[301].num,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        hoverAnimation: false,
                        // silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#FFA265", "#7AD7ED"],

                        data: [
                            {
                                value: obj[400].num,
                                name: obj[400].name,
                                id: 400
                            },
                            {
                                value: obj[401].num,
                                name: obj[401].name,
                                id: 401
                            }
                        ]
                    }
                ]
            };
            let option5 = {
                title: {
                    text: obj[500].num + obj[501].num,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        minAngle: 15,
                        hoverAnimation: false,
                        // silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: ["#9275F7", "#7BE3B8"],

                        data: [
                            {
                                value: obj[500].num,
                                name: obj[500].name,
                                id: 500
                            },
                            {
                                value: obj[501].num,
                                name: obj[501].name,
                                id: 501
                            }
                        ]
                    }
                ]
            };
            let option6 = {
                tooltip: {
                    trigger: "item",
                    formatter: "{a} <br/>{b}:{c}",
                    position: [40, -40]
                },
                title: {
                    text: allCount,
                    x: "center",
                    y: "center",
                    itemGap: 50,
                    textStyle: {
                        color: "#303030",
                        fontSize: 17,
                        fontWeight: "normal"
                    }
                },
                series: [
                    {
                        name: "房间数量",
                        type: "pie",
                        center: ["50%", "50%"],
                        radius: ["26px", "32px"],
                        startAngle: 270,
                        // minAngle: 15,
                        hoverAnimation: false,
                        // silent: true,
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        color: [
                            "#86B3E7",
                            "#FFE194",
                            "#C1E080",
                            "#F58091",
                            "#FAD650",
                            "#DC7FE2",
                            "#FFA265",
                            "#7AD7ED",
                            "#9275F7",
                            "#7BE3B8"
                        ],

                        data: [
                            {
                                value: obj[100].num,
                                name: obj[100].name,
                                id: 100
                            },
                            {
                                value: obj[101].num,
                                name: obj[101].name,
                                id: 101
                            },
                            {
                                value: obj[300].num,
                                name: obj[301].name,
                                id: 300
                            },
                            {
                                value: obj[301].num,
                                name: obj[301].name,
                                id: 301
                            },
                            {
                                value: obj[302].num,
                                name: obj[302].name,
                                id: 302
                            },
                            {
                                value: obj[303].num,
                                name: obj[303].name,
                                id: 303
                            },
                            {
                                value: obj[400].num,
                                name: obj[400].name,
                                id: 400
                            },
                            {
                                value: obj[401].num,
                                name: obj[401].name,
                                id: 401
                            },
                            {
                                value: obj[500].num,
                                name: obj[500].name,
                                id: 500
                            },
                            {
                                value: obj[501].num,
                                name: obj[501].name,
                                id: 501
                            }
                        ]
                    }
                ]
            };
            myChart1.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart2.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart3.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart4.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart5.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart6.on("click", function(param) {
                searchRoOmList(param.data.id);
            });
            myChart1.setOption(option1);
            myChart2.setOption(option2);
            myChart3.setOption(option3);
            myChart4.setOption(option4);
            myChart5.setOption(option5);
            myChart6.setOption(option6);

            //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
            window.addEventListener("resize", function() {
                // console.log("resize");
                myChart1.resize();
                myChart2.resize();
                myChart3.resize();
                myChart4.resize();
                myChart5.resize();
                myChart6.resize();
            });
        },
        getsta: function() {
            let access_token = sessionStorage.getItem("SESSIONACCESSTOKEN");
            axios({
                url: "/data/room/getRoomStatusCount",
                headers: {
                    Authorization: "Bearer " + access_token
                },
                method: "get"
            })
                .then(res => {
                    // console.log(res.data.data);
                    let obj = res.data.data;
                    this.loadEcharts(obj);
                })
                .catch(err => {
                    console.log(err);
                });
        },
        handleroomdetail:function(value) {
            // console.log(value);
        }
    },
    mounted() {
        this.loadRoomLists("");
        this.getsta();
    }
};
</script>
<style lang='less'>
