            }
            .lists-theroom-303 {
                &:hover {
                    .lists-theroom-info {
                        background: #dc7fe2;
                        border-color: transparent #dc7fe2 #dc7fe2;
                        .info-num {
                            color: #fff;
                        }
                    }
                }
                .lists-theroom-type {
                    background: #dc7fe2;
                }
            }
            .lists-theroom-400 {
                &:hover {
